import { useComponentContext } from '../../contexts/tabTypeContext/TabTypeContext';
import './GameModsList.css';

export default function GameModsList(props) {
  const { setActiveComponent } = useComponentContext();

  const goToClassicMode = () => {
    setActiveComponent('ClassicModePage');
  };
  const goToInfinityMode = () => {
    setActiveComponent('InfinityModePage');
  };
  const goToGravityMode = () => {
    setActiveComponent('GravityModePage');
  };
  const goToHundredBallsMode = () => {
    setActiveComponent('HundredBallsModePage');
  };
  const goToSurvivalMode = () => {
    setActiveComponent('SurvivalModePage');
  };

  return (
    <div className="game_mods_list-container">
      <button onClick={props.closeFunc}>Close</button>
      <button onClick={goToInfinityMode}>Infinity</button>
      <button onClick={goToGravityMode}>Gravity</button>
      <button onClick={goToClassicMode}>Classic</button>
      <button onClick={goToHundredBallsMode}>100 Balls</button>
      <button onClick={goToSurvivalMode}>Survival</button>
    </div>
  )
}