export default function GunPlatform(props) {
  const { x, y, size } = props;
  return (
    <>
      <div
        className='gun_platform'
        style={{
          width: `${size}px`,
          height: `${size}px`,
          left: `${x}px`,
          top: `${y}px`,
        }}
      ></div>
    </>
  );
}
