import { AchievementsList } from "./subComponents/AchievementsList/ActivementsList";
import './AchievementsPage.css';
import { useDispatch, useSelector } from "react-redux";
import { addLoseCount, addWinCount } from "../../../features/slices/userSlice";
import { checkPrediction } from "../../../features/achievements/checkAchievements";
import { useEffect } from "react";

export default function AchievementsPage() {

    const dispatch = useDispatch();
    const progress = useSelector((state) => state.achievements);
    const statistics = useSelector((state) => state.user.statistics);

    // <test>
    useEffect(() => {
        checkPrediction(dispatch, statistics, progress);
    }, [statistics])

    const win = () => {
        dispatch(addWinCount(1));
    }
    const lose = () => {
        dispatch(addLoseCount(1));
    }
    // </test>

    return <div className="body"><AchievementsList />
        <button className="button absolute-btn-1" onClick={win}>WIN</button>
        <button className="button absolute-btn-2" onClick={lose}>LOSE</button>
    </div>
}