export default function ExtraBall(props) {
  const { x, y, line, size, id } = props;
  return (
    <div
      id={id}
      className='element bonus-box extra-ball-bonus'
      data-line={line}
      data-activated='false'
      style={{
        left: `${x}px`,
        top: `${y}px`,
        width: `${size}px`,
        height: `${size}px`,
      }}
    >
      <div className="circle-bonus">
        <span style={{fontSize:  `${size/3}px`}}>+</span>
      </div>
    </div>
  );
}
