import { useSelector } from "react-redux"
import { translations } from "./localizationDatabase";
import { useEffect, useState } from "react";
import { buildTranslation } from "./localizationFunction";

const useLocalization = (values) => {
    const locale = useSelector(state => state.localization.locale);
    const [value, setValue] = useState(null);

    useEffect(() => {
        if (values.length > 0) {

            const array = [];
            for (let i = 0; i < values.length; i++) {
                const key = values[i];
                array.push(translations[locale][key])
            }

            setValue(array);
        } else {
            setValue({});
            const object = {};

            Object.keys(values).forEach(key => {
                object[key] = buildTranslation(locale, values[key]);
            });

            setValue(object);
        }
    }, [locale]);

    return value;
}

const useSingleLocalization = (key) => {
    const locale = useSelector(state => state.localization.locale);
    const [value, setValue] = useState({});

    useEffect(() => {
        setValue(translations[locale][key]);
    }, [locale]);

    return value;
}

export { useLocalization, useSingleLocalization }