import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    rewardsClaimed: {
        temporaryStarsCount: [{
            type: "2",
            state: false
        }],
        temporaryWinCount: false,
        temporaryBlocksDestroyed: false,
    },
    lastReset: null,
};

const dailyTasksSlice = createSlice({
    name: "tasks",
    initialState,
    reducers: {
        claimTaskReward: (state, action) => {
            const { key, type } = action.payload;
            if (type != null) {
                for (let i = 0; i < state.rewardsClaimed[key].length; i++) {
                    const element = state.rewardsClaimed[key][i];
                    if (element.type == type) {
                        element.state = true;
                        return;
                    }
                }
            }
            state.rewardsClaimed[key] = true;
        },
        resetDailyTasks: (state) => {
            Object.keys(state.rewardsClaimed).forEach((key) => {
                if (state.rewardsClaimed[key].length > 0) {
                    for (let i = 0; i < state.rewardsClaimed[key].length; i++) {
                        const element = state.rewardsClaimed[key][i];
                        element.state = false;
                    }
                } else
                    state.rewardsClaimed[key] = false;
            });

            state.lastReset = new Date().toISOString();
        },
    },
});

export const { claimTaskReward, resetDailyTasks } = dailyTasksSlice.actions;
export default dailyTasksSlice.reducer;
