import { updatePredictiveProgress } from "../slices/achievementsSlice.js";
import { achievements } from "./achievementsData.js";

export const checkPrediction = (dispatch, statistics, achievementSlice) => {
    Object.keys(achievements).forEach((key) => {
        if (achievements[key].length > 0) {
            for (let i = 0; i < achievements[key].length; i++) {
                if (achievements[key][i].progressionType != "savingProgress") continue;

                const type = achievements[key][i].type;
                const achievement = achievements[key][i].achievement;
                const progress = achievements[key][i].count;
                const currentLevel = achievementSlice.achievementProgress[key][i] || 0;

                if (
                    currentLevel < achievement.levels.length &&
                    progress >= achievement.levels[currentLevel]
                ) {
                    dispatch(
                        updatePredictiveProgress({ achievementKey: key, type: type, level: currentLevel + 1 })
                    );
                }
            }
        } else {
            if (achievements[key].progressionType == "savingProgress") {
                const achievement = achievements[key];
                const progress = statistics[key];
                const currentLevel = achievementSlice.achievementProgress[key] || 0;

                if (
                    currentLevel < achievement.levels.length &&
                    progress >= achievement.levels[currentLevel]
                ) {
                    dispatch(
                        updatePredictiveProgress({ achievementKey: key, level: currentLevel + 1 })
                    );
                }
            }
        }
    });
};
