import React from 'react';
import './App.css';
import MainPage from './app/pages/main/MainPage';
import { ComponentProvider, useComponentContext } from './app/contexts/tabTypeContext/TabTypeContext';
import ClassicModePage from './app/pages/classicModePage/ClassicModePage';
import InfinityModePage from './app/pages/infinityModePage/InfinityModePage';
import GravityModePage from './app/pages/gravityModePage/GravityModePage';
import SurvivalModePage from './app/pages/survivalModePage/SurvivalModePage';
import HundredBallsModePage from './app/pages/hundredBallsModePage/HundredBallsModePage';
import AchievementsPage from './app/pages/achievements/AchievementsPage';
import { useDailyReset } from './features/dailyTasks/dailyReset';
import DailyTasksPage from './app/pages/dailyTasks/DailyTasksPage';
import { useDispatch, useSelector } from 'react-redux';
import { changeLocale } from './features/slices/localizationSlice';
import CollectionPage from './app/pages/collection/CollectionPage';
import StoryModePage from './app/pages/storyModePage/StoryModePage';

const ParentComponent = () => {
  const { activeComponent } = useComponentContext();

  switch (activeComponent) {
    case 'MainPage':
      return <MainPage />
    case 'ClassicModePage':
      return <ClassicModePage />
    case 'StoryModePage':
      return <StoryModePage />
    case 'InfinityModePage':
      return <InfinityModePage />
    case 'GravityModePage':
      return <GravityModePage />
    case 'HundredBallsModePage':
      return <HundredBallsModePage />
    case 'SurvivalModePage':
      return <SurvivalModePage />
    case 'AchievementPage':
      return <AchievementsPage />
    case 'DailyTasksPage':
      return <DailyTasksPage />
    case 'CollectionPage':
      return <CollectionPage />
    default:
      return <MainPage />
  };
};

function App() {
  const dispatch = useDispatch();
  const locale = useSelector((state) => state.localization.locale);

  useDailyReset();

  const changeLanguage = () => {
    if (locale == 'en') {
      dispatch(changeLocale('ru'));
    } else {
      dispatch(changeLocale('en'));
    }
  }

  return (
    <>
      <div className="adds_tab">
        <div className="adds_subtab"></div>
      </div>
      <div className='globalBody'>
        <ComponentProvider>
          <ParentComponent />
        </ComponentProvider>
        <button style={{display: 'none'}} className="button lang-test-button" onClick={changeLanguage}>Change to {locale == 'en' ? 'ru' : 'en'}</button>
      </div>
    </>
  );
}

export default App;