import { ELEMENTS_SPEED } from "../../../infinityModeConstants";

export default function Gun4Element(props) {
  const { x, y, line, size, id } = props;
  return (
    <div
      id={id}
      className='element gun4Element'
      data-line={line}
      style={{
        left: `${x}px`,
        top: `${y}px`,
        width: `${size}px`,
        height: `${size}px`,
        transition: `${ELEMENTS_SPEED}s linear`,
      }}
    >
    </div>
  );
}
