import React from "react";
import { useSelector } from "react-redux";
import SkinItem from "../SkinItem/SkinItem";
import "./SkinList.css";

const SkinList = ({ category }) => {
    const collection = useSelector((state) => state.user.collection[category]);

    return (
        <div className="skin-list">
            {collection.unlocked.map((unlocked, index) => (
                <SkinItem
                    key={index}
                    unlocked={unlocked}
                    equipped={collection.equipped[index]}
                    index={index}
                    category={category}
                />
            ))}
        </div>
    );
};

export default SkinList;
