export default function Teleport(props) {
  const { x, y, name, refName, line, size, id } = props;
  return (
    <div
      id={id}
      className='element bonus-box teleport-bonus'
      data-name={name}
      data-refName={refName}
      data-activated='false'
      data-line={line}
      style={{
        left: `${x}px`,
        top: `${y}px`,
        width: `${size}px`,
        height: `${size}px`,
      }}
    >
      <div className="circle-bonus teleport-circle">
        <span style={{fontSize:  `${size/3}px`}}>{'T'}</span>
      </div>
    </div>
  );
}
