import { translations } from "./localizationDatabase";

const getTranslation = (locale, key) => {
    return translations[locale][key];
}
const buildTranslation = (locale, key) => {
    let finalValue = "";
    let temp = "";
    let symbols = "";

    for (let i = 0; i < key.length; i++) {
        const char = key[i];
        if (char == '{') {
            symbols = '{';
        }
        else if (char == '}') {
            symbols = "";
        }
        else if (symbols == '{') {
            finalValue += char;
        }
        else if (char == ' ') {
            finalValue += getTranslation(locale, temp);
            finalValue += ' ';
            temp = "";
        } else {
            temp += char;
        }
    }

    if (temp.length > 0) {
        finalValue += getTranslation(locale, temp);
        temp = "";
    }

    return finalValue;
}

export { buildTranslation }