import './TestsPopup.css';
import testsXMark from '../../pages/main/sources/tests-cross-btn.png';

export default function TestsPopup(props) {
  const { closeFunc } = props;
  return (
    <div className='tests_popup_tab popup_tab'>
      <div className='tests_popup_container popup_container'>
        <button
          className='close_tab_btn'
          onClick={closeFunc}
          style={{
            backgroundImage: `ulr('${testsXMark}')`,
            backgroundSize: 'cover',
          }}
        >
          <img src={testsXMark} alt="x" />
        </button>
        <div className="tests_popup-title popup-title">
          <span className='absolute_text'>tests</span>
        </div>
      </div>
    </div>
  );
}
