export default function FireBall(props) {
  const { x, y, damage, line, size, id } = props;
  return (
    <div
      id={id}
      className='element bonus-box fireBall-bonus'
      data-activated='false'
      data-line={line}
      data-damage={damage}
      style={{
        left: `${x}px`,
        top: `${y}px`,
        width: `${size}px`,
        height: `${size}px`,
      }}
    >
      <div className="circle-bonus fireBall-circle">
        <span style={{fontSize:  `${size/3}px`}}>{'F'}</span>
      </div>
    </div>
  );
}
