export default function FixedTrajectoryChange(props) {
  const { x, y, direction, line, size, id } = props;
  return (
    <div
      id={id}
      className='element bonus-box fixedTrajectoryChange-bonus'
      data-direction={direction}
      data-activated='false'
      data-line={line}
      style={{
        left: `${x}px`,
        top: `${y}px`,
        width: `${size}px`,
        height: `${size}px`,
      }}
    >
      <div className="circle-bonus fixedTrajectoryChange-circle">
        <span style={{fontSize:  `${size/3}px`, transform: `rotate(${direction - 90}deg)`}}>{'->'}</span>
      </div>
    </div>
  );
}
