import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    achievementProgress: {
        ingameDayCount: 0,
        winCount: 0,
        winStreak: 0,
        typedBricksDestroyed: [
            {
                type: "1",
                progress: 0
            }, {
                type: "2",
                progress: 0
            }, {
                type: "3",
                progress: 0
            }, {
                type: "4",
                progress: 0
            }, {
                type: "5",
                progress: 0
            },
        ],
        blocksDestroyed: 0,
        oneshotBlocksDestroyed: 0,
        storyModeGameWin: 0,
        overalStarsCount: 0,
        temporaryStarsCount: [
            {
                type: "1",
                progress: 0
            },
        ],
        usedBoosters: [
            {
                type: "1",
                progress: 0
            }, {
                type: "2",
                progress: 0
            }, {
                type: "3",
                progress: 0
            }, {
                type: "4",
                progress: 0
            }, {
                type: "5",
                progress: 0
            }, {
                type: "6",
                progress: 0
            },
        ],
    },
    predictiveProgress: {
        winStreak: 0,
    }
};

const achievementsSlice = createSlice({
    name: "achievements",
    initialState,
    reducers: {
        updatePredictiveProgress: (state, action) => {
            const { achievementKey, level, type } = action.payload;
            if (type != undefined && type != null) {
                for (let i = 0; i < state.predictiveProgress[achievementKey].length; i++) {
                    const element = state.predictiveProgress[achievementKey][i];
                    if (element.type == type) {
                        element.progress = level;
                        return;
                    }
                }
            }
            state.predictiveProgress[achievementKey] = level;
        },
        claimReward: (state, action) => {
            const { achievementKey, type } = action.payload;
            if (type != undefined && type != null) {
                for (let i = 0; i < state.achievementProgress[achievementKey].length; i++) {
                    const element = state.achievementProgress[achievementKey][i];
                    if (element.type == type) {
                        element.progress++;
                        return;
                    }
                }
            }

            state.achievementProgress[achievementKey]++;
        },
    },
});

export const { updatePredictiveProgress, claimReward } =
    achievementsSlice.actions;

export default achievementsSlice.reducer;
