import { useDispatch, useSelector } from "react-redux";
import { resetTemporaryBlocksDestroyed, resetTemporaryStarsCount, resetTemporaryWinsCount } from "../slices/userSlice";
import { useEffect } from "react";
import { resetDailyTasks } from "../slices/dailyTasksSlice";

const useDailyReset = () => {
    const dispatch = useDispatch();
    const lastReset = useSelector((state) => state.tasks.lastReset);

    useEffect(() => {
        const checkReset = () => {
            const now = new Date();
            const resetTime = new Date(
                now.getFullYear(), now.getMonth(), now.getDate(), 0, 0, 0, 0
            );

            if (!lastReset || new Date(lastReset) < resetTime) {
                dispatch(resetDailyTasks());
                dispatch(resetTemporaryBlocksDestroyed());
                dispatch(resetTemporaryStarsCount());
                dispatch(resetTemporaryWinsCount());
            }
        };

        checkReset();

        const interval = setInterval(checkReset, 60000);

        return () => clearInterval(interval);
    }, [lastReset, dispatch]);
};

export { useDailyReset }