import { useDispatch, useSelector } from 'react-redux';
import { claimReward } from '../../../../../features/slices/achievementsSlice';
import './AchievementView.css';
import { useLocalization } from '../../../../../features/localization/localizationHook';

export const AchievementView = ({ achievementKey, type, achievement, progress, level }) => {
    const dispatch = useDispatch();
    const savedProgress = useSelector((state) => state.achievements.predictiveProgress);

    const { title, levels, rewards, progressionType } = achievement;

    let current = progress;
    let target = levels[level];
    if (progressionType != null) {
        if (progressionType == "additive") {
            for (let i = 0; i < levels.length; i++) {
                const element = levels[i];
                if (level - 1 >= i) {
                    target += element;
                }
            }
        } else if (progressionType == "savingProgress") {
            const savedLevel = savedProgress[achievementKey];

            if (savedLevel > level && current < levels[savedLevel - 1]) {
                current = levels[savedLevel - 1];
            }
        }
    }

    const canClaimReward = current >= target;

    const claim = () => {
        dispatch(claimReward({ achievementKey, type }));
    };

    const progressPercentage = Math.min((current / target) * 100, 100);

    const translations = useLocalization({
        title: title,
        level: "level",
        progress: "progress",
        unavailable: "unavailable",
        claimReward: "claimReward"
    }) || {};

    return (
        <div className="achievement-card">
            <h3>{translations.title}</h3>
            <p>
                {translations.level}: {level}/{levels.length}
            </p>
            <div className="progress-bar">
                <div
                    className="progress-bar-fill"
                    style={{ width: `${progressPercentage}%` }}
                ></div>
            </div>
            <p>
                {translations.progress}: {current}/{target}
            </p>
            {canClaimReward ? (
                <button className="reward-button" onClick={claim}>
                    {translations.claimReward}
                </button>
            ) : (
                <button className="reward-button disabled" disabled>
                    {translations.unavailable}
                </button>
            )}
        </div>
    );
};
