import { ELEMENTS_SPEED } from '../../../survivalModeConstants';
import ElementCounter from '../elementCounter/ElementCounter';

export default function Clock(props) {
  const { x, y, line, size, id, active, timer, stacks } = props;
  // console.log(active, timer);

  return (
    <div
      id={id}
      className='element bonus-box clock-bonus'
      data-activated='false'
      data-line={line}
      data-active={active}
      style={{
        left: `${x}px`,
        top: `${y}px`,
        width: `${size}px`,
        height: `${size}px`,
        transition: `${ELEMENTS_SPEED}s linear`,
        backgroundColor: '#AA00AA',
      }}
    >
      <div className='circle-bonus laser-circle'>
        {!active ? (
          <ElementCounter size={size} stacks={stacks} />
        ) : (
          <span>{timer} s</span>
        )}
      </div>
    </div>
  );
}
