import { ELEMENTS_SPEED } from "../../../infinityModeConstants";
import ElementCounter from "../elementCounter/ElementCounter";

export default function Money(props) {
  const { x, y, line, stacks, size, id } = props;
  return (
    <div
      id={id}
      className='element money'
      data-stacks={stacks}
      data-line={line}
      style={{
        left: `${x}px`,
        top: `${y}px`,
        width: `${size}px`,
        height: `${size}px`,
        transition: `${ELEMENTS_SPEED}s linear`,
      }}
    >
      <ElementCounter size={size} stacks={stacks} isMoney={true} />
    </div>
  );
}
