import ElementCounter from "../elementCounter/ElementCounter";

export default function UntouchableBox(props) {
  const { x, y, line, stacks, size, id } = props;
  return (
    <div
      id={id}
      className='element untouchableBox'
      data-stacks={stacks}
      data-line={line}
      style={{
        left: `${x}px`,
        top: `${y}px`,
        width: `${size}px`,
        height: `${size}px`,
      }}
    >
      <ElementCounter size={size} stacks={stacks} />
    </div>
  );
}
