import { ELEMENTS_SPEED } from '../../../infinityModeConstants';
import ElementCounter from '../elementCounter/ElementCounter';

export default function Laser(props) {
  const { x, y, direction, line, size, id, stacks } = props;
  return (
    <div
      id={id}
      className='element bonus-box laser-bonus'
      data-direction={direction}
      data-activated='false'
      data-line={line}
      style={{
        left: `${x}px`,
        top: `${y}px`,
        width: `${size}px`,
        height: `${size}px`,
        transition: `${ELEMENTS_SPEED}s linear`,
      }}
    >
      <div className='circle-bonus laser-circle'>
        <span
          style={{
            fontSize: `${size / 3}px`,
            transform: direction === 'TB' ? 'rotate(90deg)' : null,
          }}
        >
          {direction === 'TB' || direction === 'LR' ? '<>' : 'X'}
        </span>
      </div>
      <ElementCounter size={size} stacks={stacks} />
    </div>
  );
}
