import React, { useState } from "react";
import "./CollectionPage.css";
import SkinList from "./subComponents/SkinList/SkinList";
import { useLocalization } from "../../../features/localization/localizationHook";

const CollectionPage = () => {
    const [activeTab, setActiveTab] = useState("balls");

    const tabs = [
        { key: "balls", label: "balls" },
        { key: "trails", label: "trails" },
        { key: "backgrounds", label: "backgrounds" },
    ];

    const translations = useLocalization({
        title: 'collection',
        balls: 'balls',
        trails: 'trails',
        backgrounds: 'backgrounds'
    }) || {};

    return (
        <div className="collection-window">
            <h1>{translations.title}</h1>
            <div className="tabs">
                {tabs.map((tab) => (
                    <button
                        key={tab.key}
                        className={`tab ${activeTab === tab.key ? "active" : ""}`}
                        onClick={() => setActiveTab(tab.key)}
                    >
                        {translations[tab.label]}
                    </button>
                ))}
            </div>
            <SkinList category={activeTab} />
        </div>
    );
};

export default CollectionPage;
