import ElementCounter from "../elementCounter/ElementCounter";

export default function Fire(props) {
  const { x, y, line, direction, stacks, exp, size, id } = props;
  return (
    <div
      id={id}
      className='element bonus-box fire-bonus'
      data-stacks={stacks}
      data-direction={direction}
      data-line={line}
      data-exp={exp}
      style={{
        left: `${x}px`,
        top: `${y}px`,
        width: `${size}px`,
        height: `${size}px`,
      }}
    >
      <ElementCounter size={size} stacks={stacks} />
      <div className="circle-bonus laser-circle">
        <span style={{fontSize:  `${size/3}px`, transform: direction === 'TB' ? 'rotate(90deg)' : null}}>{'<>'}</span>
      </div>
    </div>
  );
}
