export default function MirrorTrajectoryChange(props) {
  const { x, y, line, size, id } = props;
  return (
    <div
      id={id}
      className='element bonus-box mirrorTrajectoryChange-bonus'
      data-activated='false'
      data-line={line}
      style={{
        left: `${x}px`,
        top: `${y}px`,
        width: `${size}px`,
        height: `${size}px`,
      }}
    >
      <div className="circle-bonus mirrorTrajectoryChange-circle">
        <span style={{fontSize:  `${size/3}px`, transform: `rotate(-90deg)`}}>{'|>'}</span>
      </div>
    </div>
  );
}
