import React from "react";
import "./DailyTaskView.css";
import { useLocalization } from "../../../../../features/localization/localizationHook";

const DailyTaskView = ({ taskKey, task, type, progress, claimed, onClaim }) => {
    const { title, goal, reward } = task;
    const isComplete = progress >= goal;

    const translations = useLocalization({
        progress: "progress",
        uncompleted: 'uncompleted',
        reward: "reward",
        claimReward: "claimReward",
        title: title
    }) || {};

    return (
        <div className="daily-task-card">
            <h3>{translations.title}</h3>
            <p>{translations.progress}: {progress} / {goal}</p>
            <p>{translations.reward}: {reward.count} {reward.type}</p>
            {isComplete && !claimed ? (
                <button onClick={() => onClaim(taskKey, type)} className="claim-button">{translations.claimReward}</button>
            ) : (
                <button disabled className="claim-button disabled">
                    {translations.uncompleted}
                </button>
            )}
        </div>
    );
};

export default DailyTaskView;
