import { useState } from "react";

export default function AlertPopUp(props) {
  const { currentClickFunction, closeFunction, style, currentBonuses, setChosenBonus } = props;

  const [currentActiveBonusIndex, setCurrentActiveBonusIndex] = useState(0);

  function handleClick() {
    if (currentClickFunction && currentClickFunction !== 'level') {
      currentClickFunction();
    }
    if (currentClickFunction && currentClickFunction === 'level') {
      setChosenBonus(currentBonuses[currentActiveBonusIndex].type);
    }
    closeFunction();
  }
  function createBonusesContainer() {
    return (
      <>
        <div className="choose_bonus_panel">
          {currentBonuses.map((bonus, index) => (
            <div key={index} className="levelUp_bonus_icon" onClick={() => setCurrentActiveBonusIndex(index)}>
              <img src={bonus.url} alt={bonus.type} style={{width: '25%'}}/>
            </div>
          ))}
        </div>
      </>
    )
  }
  
  return (
    <>
      <div className='alert_popup' style={style}>
        {currentClickFunction && currentClickFunction !== 'level' ? (
          <img src='#' alt='gotten_bonus_img' />
        ) :
          (
            <>
              {createBonusesContainer()}
              <p className="bonus_info_panel">
                {currentBonuses && currentBonuses[currentActiveBonusIndex] ? currentBonuses[currentActiveBonusIndex].title : null}
              </p>
            </>
          )
        }

        <button onClick={handleClick}>OK</button>
      </div>
    </>
  );
}
