export default function MetalBox(props) {
  const { x, y, size } = props;
  return (
    <div
      className='element box metal_box'
      style={{
        left: `${x}px`,
        bottom: `${y}px`,
        width: `${size}px`,
        height: `${size}px`,
      }}
    >
    </div>
  );
}
