import Box from '../elementTypes/Box';
import Fire from '../elementTypes/Fire';
import Gun2Element from '../elementTypes/Gun2Element';
import Gun3Element from '../elementTypes/Gun3Element';
import Gun4Element from '../elementTypes/Gun4Element';
import Gun5Element from '../elementTypes/Gun5Element';
import Laser from '../elementTypes/Laser';
import Money from '../elementTypes/Money';
import Triangle from '../elementTypes/Triangle';

export default function ElementsLine(props) {
  const { row, line } = props;
  const createdElements = row.map((element, index) => {
    if (element) {
      switch (element.type) {
        case 'box':
          return (
            <Box
              key={index}
              x={element.x}
              y={element.y}
              line={line}
              stacks={element.stacks}
              size={element.size}
              id={element.id}
            />
          );
        case 'money':
          return (
            <Money
              key={index}
              x={element.x}
              y={element.y}
              line={line}
              stacks={element.stacks}
              size={element.size}
              id={element.id}
            />
          );

        case 'triangle':
          return (
            <Triangle
              key={index}
              x={element.x}
              y={element.y}
              mainAngle={element.mainAngle}
              line={line}
              stacks={element.stacks}
              size={element.size}
              id={element.id}
            />
          );
        case 'laser':
          return (
            <Laser
              key={index}
              x={element.x}
              y={element.y}
              direction={element.direction}
              line={line}
              size={element.size}
              stacks={element.stacks}
              id={element.id}
            />
          );
        case 'fire':
          return (
            <Fire
              key={index}
              x={element.x}
              y={element.y}
              direction={element.direction}
              line={line}
              size={element.size}
              stacks={element.stacks}
              id={element.id}
            />
          );
        case 'gun2Element':
          return (
            <Gun2Element
              key={index}
              x={element.x}
              y={element.y}
              size={element.size}
              id={element.id}
            />
          );
        case 'gun3Element':
          return (
            <Gun3Element
              key={index}
              x={element.x}
              y={element.y}
              size={element.size}
              id={element.id}
            />
          );
        case 'gun4Element':
          return (
            <Gun4Element
              key={index}
              x={element.x}
              y={element.y}
              size={element.size}
              id={element.id}
            />
          );
        case 'gun5Element':
          return (
            <Gun5Element
              key={index}
              x={element.x}
              y={element.y}
              size={element.size}
              id={element.id}
            />
          );
        default:
          return undefined;
      }
    } else return undefined
  });
  return createdElements;
}
