import React from "react";
import { useDispatch } from "react-redux";
import { equipCollectionItem } from "../../../../../features/slices/userSlice";
import { useLocalization } from '../../../../../features/localization/localizationHook';
import "./SkinItem.css";

const SkinItem = ({ unlocked, equipped, index, category }) => {
    const dispatch = useDispatch();

    const handleEquip = () => {
        if (unlocked) {
            dispatch(equipCollectionItem({ key: category, index }));
        }
    };

    const translations = useLocalization({
        equip: 'equip',
        equipped: 'equipped',
        locked: 'locked',
    }) || {};

    return (
        <div className="skin-item">
            <div className="preview">
                <img
                    src={`/assets/skins/${category}/${index}.png`}
                    alt="Preview"
                    className={`skin-image ${equipped ? "equipped" : ""}`}
                />
            </div>
            {unlocked ? (
                equipped ? (
                    <p className="equipped-label">{translations.equipped}</p>
                ) : (
                    <button className="equip-button" onClick={handleEquip}>
                        {translations.equip}
                    </button>
                )
            ) : (
                <p className="locked-label">{translations.locked}</p>
            )}
            {!unlocked && <p className="hint">Где получить (если нужно)</p>}
        </div>
    );
};

export default SkinItem;
