export default function ElementCounter(props) {
  return (
    <div
      className='counter_container'
      style={{ width: `${props.size}px`, height: `${props.size}px` }}
    >
      <span style={{fontSize:  `${props.size/3}px`}}>{props.isMoney ? `+${props.stacks}` : props.stacks}</span>
    </div>
  );
}
