import './ModePopup.css';
import modeXMark from '../../pages/main/sources/mode-cross-btn.png';
import GameModsList from './GameModsList';

export default function ModePopup(props) {
  const { closeFunc } = props;
  return (
    <div className='mode_popup_tab popup_tab'>
      <div className='mode_popup_container popup_container'>
        <button
          className='close_tab_btn'
          onClick={closeFunc}
          style={{
            backgroundImage: `ulr('${modeXMark}')`,
            backgroundSize: 'cover',
          }}
        >
          <img src={modeXMark} alt="x" />
        </button>
        <div className="mode_popup-title popup-title">
          <span className='absolute_text'>mode</span>
        </div>
        <GameModsList />
      </div>
    </div>
  );
}
