import './CollectionsPopup.css';
import collectionsXMark from '../../pages/main/sources/collections-cross-btn.png';

export default function CollectionsPopup(props) {
  const { closeFunc } = props;
  return (
    <div className='collections_popup_tab popup_tab'>
      <div className='collections_popup_container popup_container'>
        <button
          className='close_tab_btn'
          onClick={closeFunc}
          style={{
            backgroundImage: `ulr('${collectionsXMark}')`,
            backgroundSize: 'cover',
          }}
        >
          <img src={collectionsXMark} alt="x" />
        </button>
        <div className="collections_popup-title popup-title">
          <span className='absolute_text'>collections</span>
        </div>
      </div>
    </div>
  );
}
