import ElementCounter from "../elementCounter/ElementCounter";

export default function MagneticTrapBox(props) {
  const { x, y, line, range, gravityScale, stacks, size, id } = props;
  return (
    <div
      className='element magneticTrapBoxContainer'
      data-horizontal-magnetic-center={x+size/2}
      data-vertical-magnetic-center={y+size/2}
      data-gravity-scale={gravityScale}
      style={{
        left: `${x-size*range}px`,
        top: `${y-size*range}px`,
        width: `${size+size*range*2}px`,
        height: `${size+size*range*2}px`,
      }}
    > 
      <div
        id={id}
        className='element magneticTrapBox'
        data-stacks={stacks}
        data-range={range}
        data-line={line}
        style={{
          left: `calc(50% - ${size/2}px)`,
          top: `calc(50% - ${size/2}px)`,
          width: `${size}px`,
          height: `${size}px`,
        }}
      >
        <ElementCounter size={size} stacks={stacks} />
      </div>
    </div>
  );
}
