import { createSlice } from "@reduxjs/toolkit"

const initialState = {
    statistics: {
        ingameDayCount: 0,
        gameCount: 0,
        winCount: 0,
        winStreak: 0,
        temporaryWinCount: 0,
        loseCount: 0,
        loseInARow: 0,
        winPercent: -1,
        fastestWin: -1,
        leastWinSteps: -1,
        storyModeGameWin: 0,
        overalStarsCount: 0,
        temporaryStarsCount: [
            {
                type: "1",
                count: 0
            }, {
                type: "2",
                count: 0
            }
        ],
        usedBoosters: [
            {
                type: "1",
                count: 0
            },
            {
                type: "2",
                count: 0
            },
            {
                type: "3",
                count: 0
            },
            {
                type: "4",
                count: 0
            },
            {
                type: "5",
                count: 0
            },
            {
                type: "6",
                count: 0
            }
        ],
        blocksDestroyed: 0,
        typedBricksDestroyed: [
            {
                type: "1",
                count: 0
            }, {
                type: "2",
                count: 0
            }, {
                type: "3",
                count: 0
            }, {
                type: "4",
                count: 0
            }, {
                type: "5",
                count: 0
            }
        ],
        temporaryBlocksDestroyed: 0,
        oneshotBlocksDestroyed: 0
    },
    boosters: [
        {
            type: "1",
            count: 0
        },
        {
            type: "2",
            count: 0
        },
        {
            type: "3",
            count: 0
        },
        {
            type: "4",
            count: 0
        },
        {
            type: "5",
            count: 0
        },
        {
            type: "6",
            count: 0
        }
    ],
    collection: {
        balls: {
            equipped: [1, 0, 0, 0, 0, 0, 0],
            unlocked: [1, 1, 0, 0, 0, 0, 0]
        },
        trails: {
            equipped: [1, 0, 0, 0, 0, 0, 0],
            unlocked: [1, 0, 0, 0, 0, 0, 0]
        },
        backgrounds: {
            equipped: [1, 0, 0, 0, 0, 0, 0],
            unlocked: [1, 0, 0, 0, 0, 0, 0]
        }
    },
    currency: 0,
    energy: 0,
    musicEnabled: 1,
    soundEnabled: 1,
    nightMode: 0,
}

function getPercent(current, overal) {
    if (overal == 0) return -1;

    return Math.floor((current / overal) * 100);
}

const userSlice = createSlice({
    name: "user",
    initialState,
    reducers: {
        setIngameDayCount: (state, action) => {
            state.statistics.ingameDayCount = action.payload;
        },
        addWinCount: (state, action) => {
            const type = action.payload?.type;
            if (type == "story") {
                state.statistics.storyModeGameWin++;
            }

            state.statistics.winCount++;
            state.statistics.winStreak++;
            state.statistics.temporaryWinCount++;
            state.statistics.loseInARow = 0;

            state.statistics.gameCount++;
            state.statistics.winPercent = getPercent(state.statistics.winCount, state.statistics.gameCount);
        },
        addLoseCount: (state) => {

            state.statistics.loseCount++;
            state.statistics.loseInARow++;
            state.statistics.winStreak = 0;

            state.statistics.gameCount++;
            state.statistics.winPercent = getPercent(state.statistics.winCount, state.statistics.gameCount);
        },
        updateWinTime: (state, action) => {
            if (action.payload < state.statistics.fastestWin) {
                state.statistics.fastestWin = action.payload;
            }
        },
        updateWinStepCount: (state, action) => {
            if (action.payload <= 0) return;

            if (action.payload < state.statistics.leastWinSteps) {
                state.statistics.leastWinSteps = action.payload;
            }
        },
        addStarsCount: (state, action) => {
            if (action.payload <= 0) return;
            state.statistics.overalStarsCount += action.payload;

            for (let i = 0; i < state.statistics.temporaryStarsCount.length; i++) {
                state.statistics.temporaryStarsCount[i].count += action.payload;
            }
        },
        addBlocksDestroyed: (state, action) => {
            if (action.payload?.count <= 0) return;

            state.statistics.blocksDestroyed += action.payload?.count;
            state.statistics.temporaryBlocksDestroyed += action.payload?.count;
            if (action.payload?.isOneshot) {
                state.statistics.oneshotBlocksDestroyed += action.payload?.count;
            }
        },
        addUsedBooster: (state, action) => {
            if (action.payload?.count <= 0) return;

            for (let i = 0; i < state.statistics.usedBoosters.length; i++) {
                const element = state.statistics.usedBoosters[i];
                if (element.type == action.payload?.type) {
                    element.count += action.payload?.count;
                }
            }
        },
        changeBoosterCount: (state, action) => {
            for (let i = 0; i < state.boosters.length; i++) {
                const element = state.boosters[i];
                if (element.type == action.payload?.type) {
                    element.count += action.payload?.count;
                }
            }
        },
        changeCurrency: (state, action) => {
            state.currency += action.payload;
            if (state.currency < 0) {
                state.currency = 0;
            }
        },
        changeEnergy: (state, action) => {
            state.energy += action.payload;
            if (state.energy < 0) {
                state.energy = 0;
            }
        },
        setMusicState: (state, action) => {
            if (action.payload != 0 || action.payload != 1) return;

            state.musicEnabled = action.payload;
        },
        setSoundState: (state, action) => {
            if (action.payload != 0 || action.payload != 1) return;

            state.musicEnabled = action.payload;
        },
        setNightModeState: (state, action) => {
            if (action.payload != 0 || action.payload != 1) return;

            state.nightMode = action.payload;
        },
        equipCollectionItem: (state, action) => {
            const { key, index } = action.payload;
            for (let i = 0; i < state.collection[key].equipped.length; i++) {
                state.collection[key].equipped[i] = index == i ? 1 : 0;
            }
        },
        unlockCollectionItem: (state, action) => {
            const { key, index } = action.payload;
            for (let i = 0; i < state.collection[key].unlocked.length; i++) {
                if (index == i) state.collection[key].unlocked[i] = 1;
            }
        },
        resetTemporaryWinsCount: (state) => { state.statistics.temporaryWinCount = 0; },
        resetTemporaryStarsCount: (state) => {
            for (let i = 0; i < state.statistics.temporaryStarsCount.length; i++) {
                state.statistics.temporaryStarsCount[i].count = 0;
            }
        },
        resetTemporaryBlocksDestroyed: (state) => { state.statistics.temporaryBlocksDestroyed = 0; },
    }
});

export const {
    setIngameDayCount,
    addWinCount,
    addLoseCount,
    updateWinTime,
    updateWinStepCount,
    addStarsCount,
    addBlocksDestroyed,
    addUsedBooster,
    changeBoosterCount,
    changeCurrency,
    changeEnergy,
    setMusicState,
    setSoundState,
    setNightModeState,
    equipCollectionItem,
    unlockCollectionItem,
    resetTemporaryWinsCount,
    resetTemporaryStarsCount,
    resetTemporaryBlocksDestroyed
} = userSlice.actions;

export default userSlice.reducer;