import { useEffect, useRef, useState } from 'react';
import { useComponentContext } from '../../contexts/tabTypeContext/TabTypeContext';
import {
  BALL_SPEED,
  ELEMENTS_BY_ROW,
  ELEMENTS_SPEED,
  FIRST_GUN_LEVELS_LIST,
  STACKS_INTERVAL_DURATION,
} from './survivalModeConstants';
import Elements from './subComponents/elements/Elements';
import GunPlatform from './subComponents/elements/GunPlatform';
import './SurvivalModePage.css';

export default function SurvivalModePage() {
  const { setActiveComponent } = useComponentContext();

  const goHomeHandler = () => {
    forceStop.current = true;
    isGamePaused.current = true;
    moveBallsIntervalsRef.current.forEach((interval) =>
      interval ? clearInterval(interval) : null
    );
    clearInterval(laserDamageIntervalRef.current);
    cancelAnimationFrame(animationId.current); // Останавливаем анимацию
    stacksIntervalAccumulatedTime.current = 0; // Сбрасываем накопленное время
    stacksIntervalRangeAccumulatedTime.current = 0;
    stacksIntervalLastTime.current = 0;
    stacksIntervalRangeLastTime.current = 0;
    elementsStacksState.current = 1;
    elementsStacksRangeState.current = 1;
    openedTabGap.current = 0;
    isLoopStarted.current = false;
    setActiveComponent('MainPage');
  };

  const container = useRef(null);
  const warningZone = useRef(null);

  const [gameConfig, updateGameConfig] = useState({
    elementsByRow: ELEMENTS_BY_ROW,
    ballSpeed: BALL_SPEED,
    updated: false,
    isBallsMovingStarted: false,
  });

  const [elementsListState, setElementsListState] = useState({
    rows: [new Array(ELEMENTS_BY_ROW).fill(undefined)],
  });

  useEffect(() => {
    updateGameConfig({
      ...gameConfig,
      updated: true,
      ballsSize: container.current.clientWidth / 50,
      elementsSize: container.current.clientWidth / gameConfig.elementsByRow,
      elementsByColumn: Math.floor(
        container.current.clientWidth /
        (container.current.clientWidth / gameConfig.elementsByRow)
      ),
      columnElementsGap:
        (container.current.clientHeight -
          Math.floor(
            container.current.clientWidth /
            (container.current.clientWidth / gameConfig.elementsByRow)
          ) *
          (container.current.clientWidth / gameConfig.elementsByRow)) /
        2,
      maxLineNumber:
        Math.floor(
          (container.current.clientHeight -
            (container.current.clientWidth / 50) * 2) /
          (container.current.clientWidth / gameConfig.elementsByRow)
        ) - 1,
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [container]);

  useEffect(() => {
    setFirstGunPosition({
      left: container.current.clientWidth / 2,
      top:
        container.current.clientHeight / 2 +
        container.current.clientWidth / 20 -
        gameConfig.ballsSize * 3.1,
      isUpdated: false,
      isCreated: true,
    });
  }, [container, gameConfig]);

  const elementsStacksState = useRef(1);
  const elementsStacksRangeState = useRef(1);
  const [isGameOver, setGameOver] = useState(false);
  const isGamePaused = useRef(false);
  const isElementsPaused = useRef(false);
  const [laserBonusTimer, setLaserBonusTimer] = useState([]);
  const [multiGunBonusTimer, setMultiGunBonusTimer] = useState([]);
  const [clockBonusList, setClockBonusList] = useState([]);
  const [multiGunCounter, setMultiGunCounter] = useState(0);
  const [splashCounter, setSplashCounter] = useState(0);

  let isAnimating = false;

  const pauseGame = () => {
    isGamePaused.current = !isGamePaused.current;
  };

  const [firstGunPosition, setFirstGunPosition] = useState({});

  const [expValue, setExpValue] = useState(0);

  // Создание мяча и добавление его в массив мячей
  function createBall(
    velX,
    velY,
    startedPosition,
    damage = 1,
    type,
    gunNumber
  ) {
    const uniqueID = Date.now();
    const ball = document.createElement('div');
    ball.className = 'ball';
    ball.id = uniqueID;
    ball.style.top = startedPosition.top + 'px';

    switch (gunNumber) {
      case 0:
        ball.style.width = gameConfig.ballsSize + 'px';
        ball.style.height = gameConfig.ballsSize + 'px';
        break;
      case 1:
        ball.style.width = gameConfig.ballsSize + 'px';
        ball.style.height = gameConfig.ballsSize + 'px';
        break;
      case 2:
        ball.style.width = gameConfig.ballsSize + 'px';
        ball.style.height = gameConfig.ballsSize * 7 + 'px';
        ball.style.transformOrigin = 'bottom center';
        break;
      case 3:
        ball.style.width = gameConfig.ballsSize * 2 + 'px';
        ball.style.height = gameConfig.ballsSize * 2 + 'px';
        break;
      case 4:
        ball.style.width = gameConfig.ballsSize + 'px';
        ball.style.height = gameConfig.ballsSize + 'px';
        break;

      default:
        break;
    }

    container.current?.appendChild(ball);

    const ballElement = {
      type: type,
      elementKey: uniqueID,
      element: ball,
      x: startedPosition.left,
      y: startedPosition.top,
      velX: velX,
      velY: velY,
      damage: damage,
      touchedBallIDList: [],
    };

    return ballElement;
  }

  // Реф для остановки анимации мячей при покидании страницы
  const forceStop = useRef(false);

  // Логика запуска мячей (перенесенная с чистого js)
  function moveBall(ball, startedPosition, gunNumber, sideMove = 0) {
    let ballStopped = false;
    const isLaser = gunNumber === 2;
    const isBiggestGun = gunNumber === 3;

    // Объявление начальной позиции мяча
    ball.x = isBiggestGun
      ? startedPosition.left - gameConfig.ballsSize / 2
      : startedPosition.left;
    ball.y = isLaser
      ? startedPosition.top - gameConfig.ballsSize * 6
      : startedPosition.top;

    let isMovedToTop = false;
    let isMovedToSide = false;

    function move() {
      if (ballStopped || forceStop.current) return;
      if (!isGamePaused.current) {
        const ballRect = ball.element.getBoundingClientRect();

        // Поворот шара в направлении движения
        let angle = Math.atan2(ball.velY, ball.velX) * (180 / Math.PI);
        ball.element.style.transform = `rotate(${angle - 90}deg)`;

        // Сдвиг вправо относительно направления движения на расстояние sideMove
        if (sideMove && !isMovedToSide) {
          // Поворот мяча в направлении движения
          let angle = Math.atan2(ball.velY, ball.velX);

          // Если sideMove положительный - сдвигаем вправо, если отрицательный - влево
          let perpendicularAngle = angle + Math.PI / 2; // +90 градусов в радианах

          // Вычисляем сдвиг по оси X и Y
          let sideX = Math.cos(perpendicularAngle) * sideMove;
          let sideY = Math.sin(perpendicularAngle) * sideMove;

          // Применяем сдвиг на sideMove
          ball.x += sideX;
          ball.y += sideY;
          isMovedToSide = true;
        }

        if (!isMovedToTop) {
          ball.x += ball.velX * 10;
          ball.y += ball.velY * 10;
          isMovedToTop = true;
        }

        ball.x += ball.velX;
        ball.y += ball.velY;

        ball.element.style.left = ball.x - gameConfig.ballsSize / 2 + 'px';
        ball.element.style.top = ball.y + 'px';

        // Проверка столкновения с контейнером
        if (
          ball.y + ballRect.height >= container.current.clientHeight ||
          ball.y + ballRect.height >=
          container.current.clientHeight + gameConfig.ballsSize * 7 ||
          ball.y <= 0 - gameConfig.ballsSize * 7 * 2 ||
          ball.x - gameConfig.ballsSize / 2 <= 0 - gameConfig.ballsSize * 7 ||
          ball.x - gameConfig.ballsSize / 2 + ballRect.width >=
          container.current.clientWidth + gameConfig.ballsSize * 7
        ) {
          ballStopped = true;
          ball.element.remove();
          return;
        }

        // Проверка столкновения с другими элементами
        checkCollision(ball, isLaser);
      }
      requestAnimationFrame(move);
    }

    move();
  }

  // Проверка столкновения с другими элементами
  function checkCollision(ball, isLaser) {
    const rect1 = ball.element.getBoundingClientRect();
    const elements = document.querySelectorAll('.element');

    elements.forEach((element) => {
      const rect2 = element.getBoundingClientRect();
      if (
        rect1.right > rect2.left &&
        rect1.left < rect2.right &&
        rect1.bottom > rect2.top &&
        rect1.top < rect2.bottom
      ) {
        handleCollision(ball, element, isLaser);
      }
    });
  }

  // Обработка столкновения
  function handleCollision(ball, element, isLaser) {
    const ballRect = ball.element.getBoundingClientRect();

    const ballPrevX = ballRect.left + gameConfig.ballsSize / 2 - ball.velX;
    const ballPrevY = ballRect.top + gameConfig.ballsSize / 2 - ball.velY;

    if (element.classList.contains('triangle')) {
      const vertices = getTriangleVertices(
        element,
        element.getBoundingClientRect()
      );
      if (
        isPointInTriangle({ x: ballRect.left, y: ballRect.top }, vertices) ||
        isPointInTriangle({ x: ballRect.right, y: ballRect.top }, vertices) ||
        isPointInTriangle({ x: ballRect.left, y: ballRect.bottom }, vertices) ||
        isPointInTriangle({ x: ballRect.right, y: ballRect.bottom }, vertices)
      ) {
        if (!isLaser) {
          const side = getCollisionSideTriangle(
            ballPrevX,
            ballPrevY,
            ballRect.left + gameConfig.ballsSize / 2,
            ballRect.top + gameConfig.ballsSize / 2,
            element
          );
          // Логика для гипотенузы
          if (side === 'hypotenuse') {
            // Проверка остроты угла
            const triangleType = element.dataset.mainAngle;
            if (triangleType === 'LT' || triangleType === 'RT') {
              ball.x -= ball.velX;
              ball.y -= ball.velY;
            }
            if (triangleType === 'LT' || triangleType === 'RB') {
              // Грани типа `/`
              if (Math.sign(ball.velX) !== Math.sign(ball.velY)) {
                // Одинаковые знаки - тупой угол
                [ball.velX, ball.velY] = [-ball.velY, -ball.velX];
              } else {
                // Разные знаки - острый угол
                [ball.velX, ball.velY] = [-ball.velX, -ball.velY];
              }
            } else if (triangleType === 'LB' || triangleType === 'RT') {
              // Грани типа `\`
              if (Math.sign(ball.velX) === Math.sign(ball.velY)) {
                // Разные знаки - тупой угол
                [ball.velX, ball.velY] = [ball.velY, ball.velX];
              } else {
                // Одинаковые знаки - острый угол
                [ball.velX, ball.velY] = [-ball.velX, -ball.velY];
              }
            }
          } else {
            // Логика для сторон треугольника
            if (side === 'left' || side === 'right') {
              ball.velX = -ball.velX;
            } else if (side === 'top' || side === 'bottom') {
              if (side === 'bottom') {
                ball.x -= ball.velX;
                ball.y -= ball.velY;
              }
              ball.velY = -ball.velY;
            }
          }
        }
        // Уменьшение счетчика и удаление элемента, если он достиг 0
        const counter = element.querySelector('.counter_container span');
        if (
          (!isLaser && counter) ||
          (isLaser && !ball.touchedBallIDList.includes(element.id) && counter)
        ) {
          if (isLaser) {
            ball.touchedBallIDList.push(element.id);
          }
          const stacks = parseInt(counter.innerText);
          let ballDamage = ball.damage;
          const extraExp = stacks - ballDamage < 0 ? stacks : ballDamage;
          setExpValue((prevState) => prevState + extraExp);
          setElementsListState((prevState) => {
            const newRows = prevState.rows.map((row) =>
              row.map((currentElement) => {
                if (currentElement && currentElement.id === element.id) {
                  const newStacks = currentElement.stacks - ballDamage;
                  currentElement = { ...currentElement, stacks: newStacks };
                  if (newStacks <= 0) {
                    element.style.display = 'none';
                    return undefined;
                  }
                }
                return currentElement;
              })
            );
            return {
              rows: newRows,
            };
          });
        }
      }
    } else if (element.classList.contains('box')) {
      if (!isLaser) {
        const side = getCollisionSideBox(
          ballPrevX,
          ballPrevY,
          ballRect.left + gameConfig.ballsSize / 2,
          ballRect.top + gameConfig.ballsSize / 2,
          element
        );
        if (side === 'left' || side === 'right') {
          ball.velX = -ball.velX;
        } else if (side === 'top' || side === 'bottom') {
          if (side === 'bottom') {
            ball.x -= ball.velX;
            ball.y -= ball.velY;
          }
          ball.velY = -ball.velY;
        }
      }
      // Уменьшение счетчика и удаление элемента, если он достиг 0
      const counter = element.querySelector('.counter_container span');
      if (
        (!isLaser && counter) ||
        (isLaser && !ball.touchedBallIDList.includes(element.id) && counter)
      ) {
        if (isLaser) {
          ball.touchedBallIDList.push(element.id);
        }
        const stacks = parseInt(counter.innerText);
        let ballDamage = ball.damage;
        const extraExp = stacks - ballDamage < 0 ? stacks : ballDamage;
        setExpValue((prevState) => prevState + extraExp);
        setElementsListState((prevState) => {
          const newRows = prevState.rows.map((row) =>
            row.map((currentElement) => {
              if (currentElement && currentElement.id === element.id) {
                const newStacks = currentElement.stacks - ballDamage;
                currentElement = { ...currentElement, stacks: newStacks };
                if (newStacks <= 0) {
                  element.style.display = 'none';
                  return undefined;
                }
              }
              return currentElement;
            })
          );
          return {
            rows: newRows,
          };
        });
      }
    } else if (element.classList.contains('laser-bonus')) {
      if (!isLaser) {
        const side = getCollisionSideBox(
          ballPrevX,
          ballPrevY,
          ballRect.left + gameConfig.ballsSize / 2,
          ballRect.top + gameConfig.ballsSize / 2,
          element
        );
        if (side === 'left' || side === 'right') {
          ball.velX = -ball.velX;
        } else if (side === 'top' || side === 'bottom') {
          if (side === 'bottom') {
            ball.x -= ball.velX;
            ball.y -= ball.velY;
          }
          ball.velY = -ball.velY;
        }
      }
      // Уменьшение счетчика и удаление элемента, если он достиг 0
      const counter = element.querySelector('.counter_container span');
      let ballDamage = ball.damage;
      if (
        (!isLaser && counter) ||
        (isLaser && !ball.touchedBallIDList.includes(element.id) && counter)
      ) {
        if (isLaser) {
          ball.touchedBallIDList.push(element.id);
        }
        const stacks = parseInt(counter.innerText);
        const extraExp = stacks - ballDamage < 0 ? stacks : ballDamage;
        setExpValue((prevState) => prevState + extraExp);
        if (stacks - ballDamage <= 0) {
          startLaserBeam();
          setLaserBonusTimer((prevState) => [
            ...prevState,
            { id: element.id, active: false, timer: 10 },
          ]);
        }
        setElementsListState((prevState) => {
          const newRows = prevState.rows.map((row) =>
            row.map((currentElement) => {
              if (currentElement && currentElement.id === element.id) {
                const newStacks = currentElement.stacks - ballDamage;
                currentElement = { ...currentElement, stacks: newStacks };
                if (newStacks <= 0) {
                  element.style.display = 'none';
                  return undefined;
                }
              }
              return currentElement;
            })
          );
          return {
            rows: newRows,
          };
        });
      }
    } else if (element.classList.contains('fire-bonus')) {
      const containerRect = container.current.getBoundingClientRect();
      if (!isLaser) {
        const side = getCollisionSideBox(
          ballPrevX,
          ballPrevY,
          ballRect.left + gameConfig.ballsSize / 2,
          ballRect.top + gameConfig.ballsSize / 2,
          element
        );
        if (side === 'left' || side === 'right') {
          ball.velX = -ball.velX;
        } else if (side === 'top' || side === 'bottom') {
          if (side === 'bottom') {
            ball.x -= ball.velX;
            ball.y -= ball.velY;
          }
          ball.velY = -ball.velY;
        }
      }
      // Уменьшение счетчика и удаление элемента, если он достиг 0
      const counter = element.querySelector('.counter_container span');
      let ballDamage = ball.damage;
      if (
        (!isLaser && counter) ||
        (isLaser && !ball.touchedBallIDList.includes(element.id) && counter)
      ) {
        if (isLaser) {
          ball.touchedBallIDList.push(element.id);
        }
        const stacks = parseInt(counter.innerText);
        const extraExp = stacks - ballDamage < 0 ? stacks : ballDamage;
        setExpValue((prevState) => prevState + extraExp);
        if (stacks - ballDamage <= 0) {
          const laserRect = element.getBoundingClientRect();
          // Создание контейнера бомбы
          createLaserContainer(
            laserRect.left - laserRect.width - containerRect.left,
            laserRect.top - laserRect.height - containerRect.top,
            laserRect.width * 3,
            laserRect.height * 3,
            ballDamage,
            true
          );
        }
        setElementsListState((prevState) => {
          const newRows = prevState.rows.map((row) =>
            row.map((currentElement) => {
              if (currentElement && currentElement.id === element.id) {
                const newStacks = currentElement.stacks - ballDamage;
                currentElement = { ...currentElement, stacks: newStacks };
                if (newStacks <= 0) {
                  element.style.display = 'none';
                  return undefined;
                }
              }
              return currentElement;
            })
          );
          return {
            rows: newRows,
          };
        });
      }
    } else if (element.classList.contains('multiGun-bonus')) {
      if (!isLaser) {
        const side = getCollisionSideBox(
          ballPrevX,
          ballPrevY,
          ballRect.left + gameConfig.ballsSize / 2,
          ballRect.top + gameConfig.ballsSize / 2,
          element
        );
        if (side === 'left' || side === 'right') {
          ball.velX = -ball.velX;
        } else if (side === 'top' || side === 'bottom') {
          if (side === 'bottom') {
            ball.x -= ball.velX;
            ball.y -= ball.velY;
          }
          ball.velY = -ball.velY;
        }
      }
      // Уменьшение счетчика и удаление элемента, если он достиг 0
      const counter = element.querySelector('.counter_container span');
      let ballDamage = ball.damage;
      if (
        (!isLaser && counter) ||
        (isLaser && !ball.touchedBallIDList.includes(element.id) && counter)
      ) {
        if (isLaser) {
          ball.touchedBallIDList.push(element.id);
        }
        const stacks = parseInt(counter.innerText);
        const extraExp = stacks - ballDamage < 0 ? stacks : ballDamage;
        setExpValue((prevState) => prevState + extraExp);
        if (stacks - ballDamage <= 0) {
          setMultiGunCounter((prevState) =>
            prevState < 3 ? prevState + 1 : prevState
          );
        }
        setElementsListState((prevState) => {
          const newRows = prevState.rows.map((row) =>
            row.map((currentElement) => {
              if (currentElement && currentElement.id === element.id) {
                const newStacks = currentElement.stacks - ballDamage;
                currentElement = { ...currentElement, stacks: newStacks };
                if (newStacks <= 0) {
                  element.style.display = 'none';
                  return undefined;
                }
              }
              return currentElement;
            })
          );
          return {
            rows: newRows,
          };
        });
      }
    } else if (element.classList.contains('splash-bonus')) {
      if (!isLaser) {
        const side = getCollisionSideBox(
          ballPrevX,
          ballPrevY,
          ballRect.left + gameConfig.ballsSize / 2,
          ballRect.top + gameConfig.ballsSize / 2,
          element
        );
        if (side === 'left' || side === 'right') {
          ball.velX = -ball.velX;
        } else if (side === 'top' || side === 'bottom') {
          if (side === 'bottom') {
            ball.x -= ball.velX;
            ball.y -= ball.velY;
          }
          ball.velY = -ball.velY;
        }
      }
      // Уменьшение счетчика и удаление элемента, если он достиг 0
      const counter = element.querySelector('.counter_container span');
      let ballDamage = ball.damage;
      if (
        (!isLaser && counter) ||
        (isLaser && !ball.touchedBallIDList.includes(element.id) && counter)
      ) {
        if (isLaser) {
          ball.touchedBallIDList.push(element.id);
        }
        const stacks = parseInt(counter.innerText);
        const extraExp = stacks - ballDamage < 0 ? stacks : ballDamage;
        setExpValue((prevState) => prevState + extraExp);
        if (stacks - ballDamage <= 0) {
          setSplashCounter((prevState) =>
            prevState < 3 ? prevState + 1 : prevState
          );
        }
        setElementsListState((prevState) => {
          const newRows = prevState.rows.map((row) =>
            row.map((currentElement) => {
              if (currentElement && currentElement.id === element.id) {
                const newStacks = currentElement.stacks - ballDamage;
                currentElement = { ...currentElement, stacks: newStacks };
                if (newStacks <= 0) {
                  element.style.display = 'none';
                  return undefined;
                }
              }
              return currentElement;
            })
          );
          return {
            rows: newRows,
          };
        });
      }
    } else if (
      element.classList.contains('clock-bonus') &&
      element.dataset.active === 'false'
    ) {
      if (!isLaser) {
        const side = getCollisionSideBox(
          ballPrevX,
          ballPrevY,
          ballRect.left + gameConfig.ballsSize / 2,
          ballRect.top + gameConfig.ballsSize / 2,
          element
        );
        if (side === 'left' || side === 'right') {
          ball.velX = -ball.velX;
        } else if (side === 'top' || side === 'bottom') {
          if (side === 'bottom') {
            ball.x -= ball.velX;
            ball.y -= ball.velY;
          }
          ball.velY = -ball.velY;
        }
      }
      // Уменьшение счетчика и удаление элемента, если он достиг 0
      const counter = element.querySelector('.counter_container span');
      let ballDamage = ball.damage;
      if (
        (!isLaser && counter) ||
        (isLaser && !ball.touchedBallIDList.includes(element.id) && counter)
      ) {
        if (isLaser) {
          ball.touchedBallIDList.push(element.id);
        }
        const stacks = parseInt(counter.innerText);
        const extraExp = stacks - ballDamage < 0 ? stacks : ballDamage;
        setExpValue((prevState) => prevState + extraExp);
        if (stacks - ballDamage <= 0) {
          setClockBonusList((prevState) => [
            ...prevState,
            { id: element.id, time: 5, active: false },
          ]);
        }
        setElementsListState((prevState) => {
          const newRows = prevState.rows.map((row) =>
            row.map((currentElement) => {
              if (currentElement && currentElement.id === element.id) {
                const newStacks = currentElement.stacks - ballDamage;
                currentElement = { ...currentElement, stacks: newStacks };
                if (newStacks <= 0) {
                  currentElement = {
                    ...currentElement,
                    active: true,
                    timer: 5,
                  };
                }
              }
              return currentElement;
            })
          );
          return {
            rows: newRows,
          };
        });
      }
    }
  }

  function getTriangleVertices(triangle, rect) {
    let vertices;

    const triangleType = triangle.dataset.mainAngle;

    switch (triangleType) {
      case 'RT':
        vertices = [
          { x: rect.left, y: rect.top },
          { x: rect.right, y: rect.top },
          { x: rect.right, y: rect.bottom },
        ];
        break;
      case 'LB':
        vertices = [
          { x: rect.left, y: rect.top },
          { x: rect.left, y: rect.bottom },
          { x: rect.right, y: rect.bottom },
        ];
        break;
      case 'RB':
        vertices = [
          { x: rect.right, y: rect.top },
          { x: rect.right, y: rect.bottom },
          { x: rect.left, y: rect.bottom },
        ];
        break;
      case 'LT':
        vertices = [
          { x: rect.left, y: rect.bottom },
          { x: rect.left, y: rect.top },
          { x: rect.right, y: rect.top },
        ];
        break;
      default:
        break;
    }

    return vertices;
  }

  // Функция для проверки, находится ли точка внутри треугольника
  function isPointInTriangle(pt, v) {
    const areaOrig = Math.abs(
      (v[0].x * (v[1].y - v[2].y) +
        v[1].x * (v[2].y - v[0].y) +
        v[2].x * (v[0].y - v[1].y)) /
      2.0
    );
    const area1 = Math.abs(
      (pt.x * (v[1].y - v[2].y) +
        v[1].x * (v[2].y - pt.y) +
        v[2].x * (pt.y - v[1].y)) /
      2.0
    );
    const area2 = Math.abs(
      (v[0].x * (pt.y - v[2].y) +
        pt.x * (v[2].y - v[0].y) +
        v[2].x * (v[0].y - pt.y)) /
      2.0
    );
    const area3 = Math.abs(
      (v[0].x * (v[1].y - pt.y) +
        v[1].x * (pt.y - v[0].y) +
        pt.x * (v[0].y - v[1].y)) /
      2.0
    );
    return area1 + area2 + area3 === areaOrig;
  }
  // Функция для проверки, находится ли точка внутри ромба
  function isPointInRhombus(pt, vertices) {
    // Предполагаем, что вершины ромба передаются как массив объектов {x, y} в порядке: top, right, bottom, left
    const [top, right, bottom, left] = vertices;

    // Проверяем, находится ли точка в одном из двух треугольников
    return (
      isPointInTriangle(pt, [top, right, bottom]) || // Верхний треугольник
      isPointInTriangle(pt, [top, bottom, left]) // Нижний треугольник
    );
  }

  // Функция для проверки, находится ли точка внутри круга
  function isPointInCircle(x, y, circle) {
    const dx = x - circle.x;
    const dy = y - circle.y;
    const distanceSquared = dx * dx + dy * dy;
    const radiusSquared = circle.r * circle.r;

    return distanceSquared <= radiusSquared;
  }

  // Определение стороны столкновения c box
  function getCollisionSideBox(prevX, prevY, x, y, element) {
    const rect = element.getBoundingClientRect();

    const left = {
      x1: rect.left,
      y1: rect.top,
      x2: rect.left,
      y2: rect.bottom,
    };
    const right = {
      x1: rect.right,
      y1: rect.top,
      x2: rect.right,
      y2: rect.bottom,
    };
    const top = { x1: rect.left, y1: rect.top, x2: rect.right, y2: rect.top };
    const bottom = {
      x1: rect.left,
      y1: rect.bottom,
      x2: rect.right,
      y2: rect.bottom,
    };

    const distanceToLeft = intersectsRay(
      prevX + gameConfig.ballsSize / 2,
      prevY,
      x + gameConfig.ballsSize / 2,
      y,
      left
    );
    const distanceToRight = intersectsRay(
      prevX - gameConfig.ballsSize / 2,
      prevY,
      x - gameConfig.ballsSize / 2,
      y,
      right
    );
    const distanceToTop = intersectsRay(
      prevX,
      prevY + gameConfig.ballsSize / 2,
      x,
      y + gameConfig.ballsSize / 2,
      top
    );
    const distanceToBottom = intersectsRay(
      prevX,
      prevY - gameConfig.ballsSize / 2,
      x,
      y - gameConfig.ballsSize / 2,
      bottom
    );

    const minDistance = Math.min(
      distanceToLeft,
      distanceToRight,
      distanceToTop,
      distanceToBottom
    );

    switch (minDistance) {
      case distanceToLeft:
        return 'left';

      case distanceToRight:
        return 'right';

      case distanceToTop:
        return 'top';

      case distanceToBottom:
        return 'bottom';

      default:
        return null;
    }
  }

  // Определение стороны столкновения c triangle
  function getCollisionSideTriangle(prevX, prevY, x, y, element) {
    const rect = element.getBoundingClientRect();
    const mainAngle = element.getAttribute('data-main-angle');

    const left = {
      x1: rect.left,
      y1: rect.top,
      x2: rect.left,
      y2: rect.bottom,
    };
    const right = {
      x1: rect.right,
      y1: rect.top,
      x2: rect.right,
      y2: rect.bottom,
    };
    const top = { x1: rect.left, y1: rect.top, x2: rect.right, y2: rect.top };
    const bottom = {
      x1: rect.left,
      y1: rect.bottom,
      x2: rect.right,
      y2: rect.bottom,
    };

    let minDistance;

    const distanceToLeft = intersectsRay(
      prevX + gameConfig.ballsSize / 2,
      prevY,
      x + gameConfig.ballsSize / 2,
      y,
      left
    );
    const distanceToRight = intersectsRay(
      prevX - gameConfig.ballsSize / 2,
      prevY,
      x - gameConfig.ballsSize / 2,
      y,
      right
    );
    const distanceToTop = intersectsRay(
      prevX,
      prevY + gameConfig.ballsSize / 2,
      x,
      y + gameConfig.ballsSize / 2,
      top
    );
    const distanceToBottom = intersectsRay(
      prevX,
      prevY - gameConfig.ballsSize / 2,
      x,
      y - gameConfig.ballsSize / 2,
      bottom
    );
    let distanceToHypotenuse;
    // Проверка для гипотенузы треугольника
    let hypotenuse;
    if (mainAngle === 'RB') {
      hypotenuse = {
        x1: rect.left,
        y1: rect.bottom,
        x2: rect.right,
        y2: rect.top,
      };
      distanceToHypotenuse = intersectsRay(
        prevX + gameConfig.ballsSize / 2,
        prevY + gameConfig.ballsSize / 2,
        x + gameConfig.ballsSize / 2,
        y + gameConfig.ballsSize / 2,
        hypotenuse
      );
      minDistance = Math.min(
        distanceToRight,
        distanceToBottom,
        distanceToHypotenuse
      );
    } else if (mainAngle === 'LB') {
      hypotenuse = {
        x1: rect.right,
        y1: rect.bottom,
        x2: rect.left,
        y2: rect.top,
      };
      distanceToHypotenuse = intersectsRay(
        prevX - gameConfig.ballsSize / 2,
        prevY + gameConfig.ballsSize / 2,
        x - gameConfig.ballsSize / 2,
        y + gameConfig.ballsSize / 2,
        hypotenuse
      );
      minDistance = Math.min(
        distanceToLeft,
        distanceToBottom,
        distanceToHypotenuse
      );
    } else if (mainAngle === 'RT') {
      hypotenuse = {
        x1: rect.left,
        y1: rect.top,
        x2: rect.right,
        y2: rect.bottom,
      };
      distanceToHypotenuse = intersectsRay(
        prevX + gameConfig.ballsSize / 2,
        prevY - gameConfig.ballsSize / 2,
        x + gameConfig.ballsSize / 2,
        y - gameConfig.ballsSize / 2,
        hypotenuse
      );
      minDistance = Math.min(
        distanceToRight,
        distanceToTop,
        distanceToHypotenuse
      );
    } else if (mainAngle === 'LT') {
      hypotenuse = {
        x1: rect.right,
        y1: rect.top,
        x2: rect.left,
        y2: rect.bottom,
      };
      distanceToHypotenuse = intersectsRay(
        prevX - gameConfig.ballsSize / 2,
        prevY - gameConfig.ballsSize / 2,
        x - gameConfig.ballsSize / 2,
        y - gameConfig.ballsSize / 2,
        hypotenuse
      );
      minDistance = Math.min(
        distanceToLeft,
        distanceToTop,
        distanceToHypotenuse
      );
    }

    switch (minDistance) {
      case distanceToLeft:
        return 'left';

      case distanceToRight:
        return 'right';

      case distanceToTop:
        return 'top';

      case distanceToBottom:
        return 'bottom';

      case distanceToHypotenuse:
        return 'hypotenuse';

      default:
        return null;
    }
  }

  // Определение стороны столкновения c rhombus (money)
  function getCollisionSideRhombus(prevX, prevY, x, y, element) {
    const rect = element.getBoundingClientRect();

    // Предполагаем, что ромб имеет 4 стороны: верхняя левая, верхняя правая, нижняя левая, нижняя правая.
    // Вершины ромба (примерно по диагоналям)
    const leftTop = {
      x1: (rect.left + rect.right) / 2,
      y1: rect.top,
      x2: rect.left,
      y2: (rect.top + rect.bottom) / 2,
    };

    const rightBottom = {
      x1: rect.right,
      y1: (rect.top + rect.bottom) / 2,
      x2: (rect.left + rect.right) / 2,
      y2: rect.bottom,
    };

    const leftBottom = {
      x1: rect.left,
      y1: (rect.top + rect.bottom) / 2,
      x2: (rect.left + rect.right) / 2,
      y2: rect.bottom,
    };

    const rightTop = {
      x1: rect.right,
      y1: (rect.top + rect.bottom) / 2,
      x2: (rect.left + rect.right) / 2,
      y2: rect.top,
    };

    let minDistance;

    // Проверка пересечения с каждой из сторон ромба
    const distanceToLeftTop = intersectsRay(prevX, prevY, x, y, leftTop);
    const distanceToRightTop = intersectsRay(prevX, prevY, x, y, rightTop);
    const distanceToRightBottom = intersectsRay(
      prevX,
      prevY,
      x,
      y,
      rightBottom
    );
    const distanceToLeftBottom = intersectsRay(prevX, prevY, x, y, leftBottom);

    // Найдём минимальное расстояние для определения стороны столкновения
    minDistance = Math.min(
      distanceToLeftTop,
      distanceToRightTop,
      distanceToRightBottom,
      distanceToLeftBottom
    );

    switch (minDistance) {
      case distanceToLeftTop:
        return 'leftTop';
      case distanceToRightTop:
        return 'rightTop';
      case distanceToRightBottom:
        return 'rightBottom';
      case distanceToLeftBottom:
        return 'leftBottom';
      default:
        return null;
    }
  }

  // Проверка пересечения линий
  function intersectsRay(x1, y1, x2, y2, line) {
    const dx1 = x2 - x1;
    const dy1 = y2 - y1;
    const dx2 = line.x2 - line.x1;
    const dy2 = line.y2 - line.y1;

    const det = dx1 * dy2 - dx2 * dy1;

    if (det === 0) return 10000; // Линии параллельны

    const t1 = -((line.y1 - y1) * dx2 - (line.x1 - x1) * dy2) / det;

    // if (t1 < 0) return 10000; // Луч пересекается с линией в обратном направлении

    const intersectionX = x1 + t1 * dx1;
    const intersectionY = y1 + t1 * dy1;
    const distance = Math.sqrt(
      Math.pow(intersectionX - x2, 2) + Math.pow(intersectionY - y2, 2)
    );

    return distance;
  }

  function createLaserContainer(x, y, width, height, damage, isFire) {
    const laserContainer = document.createElement('div');
    laserContainer.className = 'laser-container';
    laserContainer.style.backgroundColor = isFire ? '#e78f00' : null;
    laserContainer.style.left = x + 'px';
    laserContainer.style.top = y + 'px';
    laserContainer.style.width = width + 'px';
    laserContainer.style.height = height + 'px';
    container.current.appendChild(laserContainer);

    checkIntersections(laserContainer.getBoundingClientRect(), damage, isFire);

    setTimeout(() => {
      laserContainer.remove();
    }, 100);
  }

  // Проверка пересечения лазера с элементами
  function checkIntersections(laserRect, damage, isFire) {
    const elements = document.querySelectorAll('.element');
    elements.forEach((element) => {
      const elementRect = element.getBoundingClientRect();
      if (
        laserRect.left < elementRect.right &&
        laserRect.right > elementRect.left &&
        laserRect.top < elementRect.bottom &&
        laserRect.bottom > elementRect.top
      ) {
        decreaseStacks(element, damage, isFire);
      }
    });
  }

  // Снятие стаков с элемента
  function decreaseStacks(element, damage, isFire) {
    setElementsListState((prevState) => {
      const newRows = prevState.rows.map((row) =>
        row.map((currentElement) => {
          if (currentElement && currentElement.id === element.id) {
            const newStacks = currentElement.stacks - damage;
            const extraExp = newStacks < 0 ? damage + newStacks : damage;
            setExpValue((prevState) => prevState + extraExp);
            currentElement = { ...currentElement, stacks: newStacks };
            if (newStacks <= 0 || isFire) {
              element.style.display = 'none';
              return undefined;
            }
          }
          return currentElement;
        })
      );
      return {
        rows: newRows,
      };
    });
  }

  function checkBonusEffect(element, damage, isFire) {
    const counter = element.querySelector('.counter_container span');
    let ballDamage = damage;
    const stacks = parseInt(counter.innerText);
    const containerRect = container.current.getBoundingClientRect();
    if (stacks - ballDamage <= 0 || isFire) {
      if (element.classList.contains('laser-bonus')) {
        setLaserBonusTimer((prevState) => [
          ...prevState,
          { id: element.id, active: false, timer: 10 },
        ]);
      } else if (element.classList.contains('fire-bonus')) {
        const laserRect = element.getBoundingClientRect();
        // Создание контейнера бомбы
        createLaserContainer(
          laserRect.left - laserRect.width - containerRect.left,
          laserRect.top - laserRect.height - containerRect.top,
          laserRect.width * 3,
          laserRect.height * 3,
          ballDamage,
          true
        );
      } else if (element.classList.contains('multiGun-bonus')) {
        setMultiGunCounter((prevState) =>
          prevState < 3 ? prevState + 1 : prevState
        );
      } else if (element.classList.contains('splash-bonus')) {
        setSplashCounter((prevState) =>
          prevState < 3 ? prevState + 1 : prevState
        );
      } else if (
        element.classList.contains('clock-bonus') &&
        element.dataset.active === 'false'
      ) {
        setClockBonusList((prevState) => [
          ...prevState,
          { id: element.id, time: 5, active: false },
        ]);
      }
    }
  }

  // Подсчет количества линий для определения приближения завершения игры
  const [linesCounterState, setLinesCounterState] = useState(1);
  function checkIslastStepToWin(prevState) {
    const elementsListClone = [...prevState.rows];
    while (
      elementsListClone.length > 0 &&
      elementsListClone[0].every((element) => element === undefined)
    ) {
      elementsListClone.shift();
    }
    setLinesCounterState(elementsListClone.length);
  }

  // useEffect(() => {
  //   if (linesCounterState > gameConfig.maxLineNumber + 1) {
  //     setGameOver(true);
  //   }
  // }, [gameConfig.maxLineNumber, linesCounterState]);
  useEffect(() => {
    if (isGameOver) {
      forceStop.current = true;
      isGamePaused.current = true;
      moveBallsIntervalsRef.current.forEach((interval) =>
        interval ? clearInterval(interval) : null
      );
    }
  }, [isGameOver]);

  // Объявление состояний для отслеживания изменения уровня
  const [firstGunLevel, setFirstGunLevel] = useState(1);

  useEffect(() => {
    if (firstGunLevel) {
      clearInterval(moveBallsIntervalsRef.current[0]);
      startBalls(firstGunPosition, FIRST_GUN_LEVELS_LIST[firstGunLevel - 1], 0);
    }
  }, [firstGunLevel]);

  useEffect(() => {
    if (
      gameConfig.updated &&
      !gameConfig.isBallsMovingStarted &&
      firstGunPosition.isCreated &&
      firstGunPosition.top
    ) {
      startBalls(firstGunPosition, FIRST_GUN_LEVELS_LIST[0], 0);
      addNewLineClassicMode(ELEMENTS_BY_ROW, false);
      startElements();
      // Стартуем анимацию увеличения стаков
      animationId.current = requestAnimationFrame(intervalLoop);
      gameConfig.isBallsMovingStarted = true;
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [gameConfig.updated, firstGunPosition]);

  // Рефы для управления направлением полета мячей
  const clientXRef = useRef(null);
  const clientYRef = useRef(null);
  const moveBallsIntervalsRef = useRef(new Array(5));

  function startElements() {
    let customSkipFlag = 0;
    setInterval(() => {
      if (!isGamePaused.current && !isElementsPaused.current) {
        addNewLineClassicMode(ELEMENTS_BY_ROW, customSkipFlag !== 9);
        customSkipFlag < 9 ? customSkipFlag++ : (customSkipFlag = 0);
      }
    }, 1000 * ELEMENTS_SPEED);
  }

  function degToRad(degrees) {
    return degrees * (Math.PI / 180);
  }
  // Функция для поворота вектора на заданный угол
  function rotateVector(velX, velY, angle) {
    const newVelX = velX * Math.cos(angle) - velY * Math.sin(angle);
    const newVelY = velX * Math.sin(angle) + velY * Math.cos(angle);
    return { newVelX, newVelY };
  }

  // Зауск мячей из пушки
  function startBalls(position, levelConfig, gunNumber = 0) {
    const startedPosition = {
      left: position.left,
      top: position.top,
    };
    startDrawingTrajectory();
    const moveBallsInterval = setInterval(() => {
      if (!isGamePaused.current) {
        if (
          isLaserBonusActive.current &&
          startedPosition.left &&
          startedPosition.top
        ) {
        } else {
          // Если бонус деактивирован, останавливаем лазер и продолжаем запуск мячей
          if (laserIntervalRef.current) {
            stopLaserBeam(trajectoryLaserElement);
            stopLaserBeam(trajectoryLaserElement2);
            stopLaserBeam(trajectoryLaserElement3);
            stopLaserBeam(trajectoryLaserElement4);
            stopLaserBeam(trajectoryLaserElement5);
            stopLaserBeam(trajectoryLaserElement6);
          }

          // Расчет направления для мячей
          let velX = 0;
          let velY = -levelConfig.speed;
          if (clientXRef.current || clientYRef.current) {
            // Позиция мыши в момент удержания кнопки мыши
            const releaseX =
              clientXRef.current -
              container.current.getBoundingClientRect().left;
            const releaseY =
              clientYRef.current -
              container.current.getBoundingClientRect().top;

            // Вычисление направления движения
            const deltaX =
              releaseX - startedPosition.left - gameConfig.ballsSize / 2;
            const deltaY = releaseY - startedPosition.top;
            const distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY);

            // Задание скорости и направления
            velX = (deltaX / distance) * levelConfig.speed; // Коэффициент скорости
            velY = (deltaY / distance) * levelConfig.speed;
          }
          const ballConfig = createBall(
            velX,
            velY,
            startedPosition,
            levelConfig.damage,
            levelConfig.type,
            gunNumber
          );
          moveBall(ballConfig, startedPosition, gunNumber);
          if (isMultiGunActive.current) {
            // Угол в радианах для поворота на 30 градусов
            const angleLeft1 = degToRad(-60); // Левый поворот
            const angleRight1 = degToRad(60); // Правый поворот
            const angleLeft2 = degToRad(-120); // Левый поворот 2
            const angleRight2 = degToRad(120); // Правый поворот 2
            const angleBottom = degToRad(180); // Нижний поворот

            // Рассчитываем отклонение влево на 30 и 60 градусов
            const { newVelX: velXLeft1, newVelY: velYLeft1 } = rotateVector(
              velX,
              velY,
              angleLeft1
            );

            const { newVelX: velXLeft2, newVelY: velYLeft2 } = rotateVector(
              velX,
              velY,
              angleLeft2
            );

            // Рассчитываем отклонение вправо на 30 и 60 градусов
            const { newVelX: velXRight1, newVelY: velYRight1 } = rotateVector(
              velX,
              velY,
              angleRight1
            );

            const { newVelX: velXRight2, newVelY: velYRight2 } = rotateVector(
              velX,
              velY,
              angleRight2
            );

            // Рассчитываем отклонение вправо на 180 градусов
            const { newVelX: velXBottom, newVelY: velYBottom } = rotateVector(
              velX,
              velY,
              angleBottom
            );

            const multiBallConfigLeft1 = createBall(
              velXLeft1,
              velYLeft1,
              startedPosition,
              levelConfig.damage,
              levelConfig.type,
              gunNumber
            );
            const multiBallConfigRight1 = createBall(
              velXRight1,
              velYRight1,
              startedPosition,
              levelConfig.damage,
              levelConfig.type,
              gunNumber
            );
            const multiBallConfigLeft2 = createBall(
              velXLeft2,
              velYLeft2,
              startedPosition,
              levelConfig.damage,
              levelConfig.type,
              gunNumber
            );
            const multiBallConfigRight2 = createBall(
              velXRight2,
              velYRight2,
              startedPosition,
              levelConfig.damage,
              levelConfig.type,
              gunNumber
            );
            const multiBallConfigBottom = createBall(
              velXBottom,
              velYBottom,
              startedPosition,
              levelConfig.damage,
              levelConfig.type,
              gunNumber
            );
            moveBall(multiBallConfigLeft1, startedPosition, gunNumber);
            moveBall(multiBallConfigRight1, startedPosition, gunNumber);
            moveBall(multiBallConfigLeft2, startedPosition, gunNumber);
            moveBall(multiBallConfigRight2, startedPosition, gunNumber);
            moveBall(multiBallConfigBottom, startedPosition, gunNumber);
          }
        }
      }
    }, levelConfig.rate);
    moveBallsIntervalsRef.current[gunNumber] = moveBallsInterval;
  }

  // Отображение траектории и добавление слушателей событий
  const trajectoryElement = useRef(null);
  const trajectoryLaserElement = useRef(null);
  const trajectoryLaserElement2 = useRef(null);
  const trajectoryLaserElement3 = useRef(null);
  const trajectoryLaserElement4 = useRef(null);
  const trajectoryLaserElement5 = useRef(null);
  const trajectoryLaserElement6 = useRef(null);

  // Стейт для отслеживания клика
  const [isMouseHolded, setMouseHolded] = useState(false);

  const gun1Element = useRef(null);

  const [isGun1Active, setGun1Active] = useState(true);

  function startDrawingTrajectory() {
    if (isAnimating || isGameOver) return;
    clearTrajectory();

    const clientX =
      firstGunPosition.left + container.current.getBoundingClientRect().left;
    const clientY =
      firstGunPosition.top +
      container.current.getBoundingClientRect().top -
      gameConfig.ballsSize;

    const startX = firstGunPosition.left + gameConfig.ballsSize / 2;
    const startY = firstGunPosition.top + gameConfig.ballsSize / 2;

    const endX =
      clientX -
      container.current.getBoundingClientRect().left +
      gameConfig.ballsSize / 2;
    const endY =
      clientY -
      container.current.getBoundingClientRect().top +
      gameConfig.ballsSize / 2;

    drawTrajectory(startX, startY, endX, endY);
  }

  function startTrajectoryUpdating() {
    setMouseHolded(true);
  }

  function stopTrajectoryUpdating() {
    setMouseHolded(false);
  }

  function mouseMoveHandler(event) {
    updateTrajectory(event);
    if (isLaserBonusActive.current) {
      updateLaserBeam(event);
      setTrajectoryLaserActive(true);
    } else {
      setTrajectoryLaserActive(false);
    }
    if (isGun1Active) {
      updateGunRotation(event, gun1Element);
    }
  }

  const [isTrajectoryLaserActive, setTrajectoryLaserActive] = useState(false);
  const laserDamageIntervalRef = useRef(null);

  useEffect(() => {
    if (isTrajectoryLaserActive) {
      laserDamageIntervalRef.current = setInterval(() => {
        if (!isGamePaused.current) {
          const startX = firstGunPosition.left + gameConfig.ballsSize / 2;
          const startY = firstGunPosition.top + gameConfig.ballsSize / 2;

          const { endX, endY } = calculateLaserEndPosition(
            startX,
            startY,
            clientXRef.current -
            container.current.getBoundingClientRect().left +
            gameConfig.ballsSize / 2,
            clientYRef.current -
            container.current.getBoundingClientRect().top +
            gameConfig.ballsSize / 2
          );

          checkLaserIntersections(startX, startY, endX, endY);
          if (isMultiGunActive.current) {
            // Конечная точка по текущей позиции (до поворота)
            const targetX =
              clientXRef.current -
              container.current.getBoundingClientRect().left +
              gameConfig.ballsSize / 2;
            const targetY =
              clientYRef.current -
              container.current.getBoundingClientRect().top +
              gameConfig.ballsSize / 2;

            // Вычисляем вектор направления до цели
            const deltaX = targetX - startX;
            const deltaY = targetY - startY;

            // Поворачиваем вектор на 60 градусов
            const angle2 = degToRad(60); // Конвертируем 60 градусов в радианы
            const { newVelX: newVelX2, newVelY: newVelY2 } = rotateVector(
              deltaX,
              deltaY,
              angle2
            );

            // Вычисляем конечные координаты лазера на границе контейнера
            const { endX: endX2, endY: endY2 } = calculateLaserEndPosition(
              startX,
              startY,
              startX + newVelX2,
              startY + newVelY2
            );
            // Поворачиваем вектор на 60 градусов
            const angle3 = degToRad(-60); // Конвертируем 60 градусов в радианы
            const { newVelX: newVelX3, newVelY: newVelY3 } = rotateVector(
              deltaX,
              deltaY,
              angle3
            );

            // Вычисляем конечные координаты лазера на границе контейнера
            const { endX: endX3, endY: endY3 } = calculateLaserEndPosition(
              startX,
              startY,
              startX + newVelX3,
              startY + newVelY3
            );
            // Поворачиваем вектор на 60 градусов
            const angle4 = degToRad(120); // Конвертируем 60 градусов в радианы
            const { newVelX: newVelX4, newVelY: newVelY4 } = rotateVector(
              deltaX,
              deltaY,
              angle4
            );

            // Вычисляем конечные координаты лазера на границе контейнера
            const { endX: endX4, endY: endY4 } = calculateLaserEndPosition(
              startX,
              startY,
              startX + newVelX4,
              startY + newVelY4
            );
            // Поворачиваем вектор на 60 градусов
            const angle5 = degToRad(-120); // Конвертируем 60 градусов в радианы
            const { newVelX: newVelX5, newVelY: newVelY5 } = rotateVector(
              deltaX,
              deltaY,
              angle5
            );

            // Вычисляем конечные координаты лазера на границе контейнера
            const { endX: endX5, endY: endY5 } = calculateLaserEndPosition(
              startX,
              startY,
              startX + newVelX5,
              startY + newVelY5
            );
            // Поворачиваем вектор на 60 градусов
            const angle6 = degToRad(180); // Конвертируем 60 градусов в радианы
            const { newVelX: newVelX6, newVelY: newVelY6 } = rotateVector(
              deltaX,
              deltaY,
              angle6
            );

            // Вычисляем конечные координаты лазера на границе контейнера
            const { endX: endX6, endY: endY6 } = calculateLaserEndPosition(
              startX,
              startY,
              startX + newVelX6,
              startY + newVelY6
            );

            checkLaserIntersections(startX, startY, endX2, endY2);
            checkLaserIntersections(startX, startY, endX3, endY3);
            checkLaserIntersections(startX, startY, endX4, endY4);
            checkLaserIntersections(startX, startY, endX5, endY5);
            checkLaserIntersections(startX, startY, endX6, endY6);
          }
        }
      }, 200);
    } else {
      clearInterval(laserDamageIntervalRef.current);
      stopLaserBeam(trajectoryLaserElement);
      stopLaserBeam(trajectoryLaserElement2);
      stopLaserBeam(trajectoryLaserElement3);
      stopLaserBeam(trajectoryLaserElement4);
      stopLaserBeam(trajectoryLaserElement5);
      stopLaserBeam(trajectoryLaserElement6);
    }
  }, [isTrajectoryLaserActive]);

  function updateTrajectory(event) {
    const { clientX, clientY } = event;
    clientXRef.current = clientX;
    clientYRef.current = clientY;
    const startX = firstGunPosition.left + gameConfig.ballsSize / 2;
    const startY = firstGunPosition.top + gameConfig.ballsSize / 2;
    const endX =
      clientX -
      container.current.getBoundingClientRect().left +
      gameConfig.ballsSize / 2;
    const endY =
      clientY -
      container.current.getBoundingClientRect().top +
      gameConfig.ballsSize / 2;
    drawTrajectory(startX, startY, endX, endY);
  }

  function updateGunRotation(event, gunElement) {
    const { clientX, clientY } = event;
    const gunElementRect = gunElement.current.getBoundingClientRect();
    clientXRef.current = clientX;
    clientYRef.current = clientY;
    const startX =
      gunElementRect.left + gunElementRect.width / 2 + gameConfig.ballsSize / 2;
    const startY = gunElementRect.top + gameConfig.ballsSize * 2.1;
    const endX = clientX + gameConfig.ballsSize / 2;
    const endY = clientY + gameConfig.ballsSize / 2;

    const angle = (Math.atan2(endY - startY, endX - startX) * 180) / Math.PI;

    gunElement.current.style.transformOrigin = `bottom center`;
    gunElement.current.style.transform = `rotate(${angle + 90}deg)`;
  }

  const isLaserBonusActive = useRef(false);

  function drawTrajectory(startX, startY, endX, endY) {
    if (isNaN(firstGunPosition.left)) return;
    clearTrajectory();

    const deltaX = endX - startX;
    const deltaY = endY - startY;
    const distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY);

    let velX = (deltaX / distance) * 8;
    let velY = (deltaY / distance) * 8;

    trajectoryElement.current = document.createElement('div');
    trajectoryElement.current.classList.add('trajectory');

    let currentX = startX;
    let currentY = startY;
    let currentDistance = 0;

    let forceStopDrawing = false;

    // Множитель является определителем длинны траектории
    while (
      currentDistance < container.current.clientHeight * 1 &&
      !forceStopDrawing
    ) {
      currentX += velX;
      currentY += velY;
      currentDistance += Math.sqrt(
        (currentX - startX) ** 2 + (currentY - startY) ** 2
      );

      if (currentX <= 0 || currentX >= container.current.clientWidth) {
        velX *= -1;
        currentX = Math.min(
          Math.max(currentX, 0),
          container.current.clientWidth
        );
        forceStopDrawing = true;
      }

      if (currentY <= 0 || currentY >= container.current.clientHeight) {
        velY *= -1;
        currentY = Math.min(
          Math.max(currentY, 0),
          container.current.clientHeight
        );
        forceStopDrawing = true;
      }

      const segment = createTrajectorySegment(
        startX,
        startY,
        currentX,
        currentY
      );
      trajectoryElement.current.appendChild(segment);
      startX = currentX;
      startY = currentY;
    }

    container.current.appendChild(trajectoryElement.current);
  }

  function createTrajectorySegment(x1, y1, x2, y2) {
    const segment = document.createElement('div');
    segment.classList.add('trajectory-segment');

    const length = Math.sqrt((x2 - x1) ** 2 + (y2 - y1) ** 2);
    const angle = (Math.atan2(y2 - y1, x2 - x1) * 180) / Math.PI;

    segment.style.width = `${length / 2}px`;
    segment.style.left = 0 - length / 2 - gameConfig.ballsSize / 4 + 'px';
    segment.style.transform = `translate(${x1}px, ${y1}px) rotate(${angle}deg)`;

    return segment;
  }

  function clearTrajectory() {
    if (trajectoryElement.current) {
      trajectoryElement.current.remove();
      trajectoryElement.current = null;
    }
  }
  // Создание новой строки блоков со сдвигом вниз
  function addNewLineClassicMode(size, customForceSkip) {
    let gunPlatformRect, containerRect;
    if (gunPlatformRef.current && container.current) {
      gunPlatformRect = gunPlatformRef.current.getBoundingClientRect();
      containerRect = container.current.getBoundingClientRect();
    }
    // Обновляем состояние с использованием функции для доступа к актуальному состоянию
    setElementsListState((prevState) => {
      prevState.rows.forEach((row) =>
        row.forEach((element, index) => {
          if (
            element &&
            (element.type === 'box' || element.type === 'triangle')
          ) {
            const rect2 = {
              left: element.x + containerRect?.left,
              right: element.x + element.size + containerRect?.left,
              top: element.y + containerRect?.top,
              bottom: element.y + element.size + containerRect?.top,
            };
            switch (element.direction) {
              case 'top':
                element.y += element.size / 2 / 10;
                element.x += element.moveX;
                break;

              case 'left':
                element.x += element.size / 2 / 10;
                element.y += element.moveY;
                break;

              case 'bottom':
                element.y -= element.size / 2 / 10;
                element.x += element.moveX;
                break;

              case 'right':
                element.x -= element.size / 2 / 10;
                element.y += element.moveY;
                break;

              default:
                break;
            }
            if (
              gunPlatformRect &&
              gunPlatformRect.right > rect2.left &&
              gunPlatformRect.left < rect2.right &&
              gunPlatformRect.bottom > rect2.top &&
              gunPlatformRect.top < rect2.bottom
            ) {
              setGameOver(true);
            }
          }
        })
      );
      if (prevState && prevState.rows && prevState.rows.length > 1) {
        checkIslastStepToWin(prevState);
      }

      if (customForceSkip) {
        return {
          rows: [...prevState.rows],
        };
      } else {
        const newLineTop = generateLine(newLineGenerating(size), 'top');
        const newLineLeft = generateLine(
          newLineGenerating(gameConfig.elementsByColumn),
          'left'
        );
        const newLineBottom = generateLine(newLineGenerating(size), 'bottom');
        const newLineRight = generateLine(
          newLineGenerating(gameConfig.elementsByColumn),
          'right'
        );

        return {
          rows: [
            ...prevState.rows,
            ...[
              ...newLineTop.rows,
              ...newLineLeft.rows,
              ...newLineBottom.rows,
              ...newLineRight.rows,
            ],
          ],
        };
      }
    });
    // if (!customForceSkip) {
    //   elementsStacksState.current = elementsStacksState.current + 10;
    // }
  }

  function calculateAxisGap(side, x, y, stepSize) {
    // Определение позиции блока
    const centerX = firstGunPosition.left - gameConfig.elementsSize / 2;
    const centerY = firstGunPosition.top - gameConfig.elementsSize / 2;

    // Вычисляем разницу по каждой оси
    const deltaX = centerX - x;
    const deltaY = centerY - y;

    let moveX = 0,
      moveY = 0;
    // Вычисляем соотношение для перемещения
    switch (side) {
      case 'top':
      case 'bottom':
        // Двигаемся вверх или вниз с фиксированным шагом по Y
        moveY = side === 'top' ? stepSize : -stepSize;

        // Рассчитываем пропорциональное смещение по X
        const ratioX = deltaX / Math.abs(deltaY);
        moveX = ratioX * Math.abs(moveY);
        break;

      case 'left':
      case 'right':
        // Двигаемся влево или вправо с фиксированным шагом по X
        moveX = side === 'left' ? stepSize : -stepSize;

        // Рассчитываем пропорциональное смещение по Y
        const ratioY = deltaY / Math.abs(deltaX);
        moveY = ratioY * Math.abs(moveX);
        break;

      default:
        return;
    }

    return { left: moveX, top: moveY };
  }

  // Генерация новой линии со случайными блоками (предпоследний блок - всегда бонусный мяч)
  function generateLine(config, side) {
    const rewritedElements = config.rows.map((row, rowIndex) => {
      // скорость перемещения
      const stepSize = gameConfig.elementsSize / 20;
      return row.map((cell, cellIndex) => {
        if (cell) {
          if (cell.type === 'box' || cell.type === 'triangle') {
            let x, y, sideMove;
            switch (side) {
              case 'top':
                x = cellIndex * gameConfig.elementsSize;
                y = 0 - rowIndex * gameConfig.elementsSize;

                sideMove = calculateAxisGap(side, x, y, stepSize);

                cell.x = x;
                cell.y = y - gameConfig.elementsSize;

                cell.moveX = sideMove.left;
                cell.moveY = stepSize;
                break;

              case 'left':
                x = 0 - rowIndex * gameConfig.elementsSize;
                y =
                  cellIndex * gameConfig.elementsSize +
                  gameConfig.columnElementsGap;

                sideMove = calculateAxisGap(side, x, y, stepSize);

                cell.x = x - gameConfig.elementsSize;
                cell.y = y;

                cell.moveX = stepSize;
                cell.moveY = sideMove.top;
                break;

              case 'bottom':
                x = cellIndex * gameConfig.elementsSize;
                y =
                  container.current.clientHeight +
                  rowIndex * gameConfig.elementsSize;

                sideMove = calculateAxisGap(side, x, y, stepSize);

                cell.x = x;
                cell.y = y;

                cell.moveX = sideMove.left;
                cell.moveY = stepSize;
                break;

              case 'right':
                x =
                  container.current.clientWidth +
                  rowIndex * gameConfig.elementsSize;
                y =
                  cellIndex * gameConfig.elementsSize +
                  gameConfig.columnElementsGap;

                sideMove = calculateAxisGap(side, x, y, stepSize);

                cell.x = x + gameConfig.elementsSize;
                cell.y = y;

                cell.moveX = stepSize;
                cell.moveY = sideMove.top;
                break;

              default:
                break;
            }

            cell.id = `element-${side}-${rowIndex}-${cellIndex}-${Date.now()}`;
            cell.direction = side;

            return cell;
          } else {
            let x, y;
            switch (side) {
              case 'top':
                x = cellIndex * gameConfig.elementsSize;
                y = 0 - rowIndex * gameConfig.elementsSize;

                cell.x = x;
                cell.y =
                  y +
                  gameConfig.elementsSize +
                  gameConfig.elementsSize *
                  Math.random() *
                  (gameConfig.elementsByColumn / 2 - 2);

                break;

              case 'left':
                x = 0 - rowIndex * gameConfig.elementsSize;
                y =
                  cellIndex * gameConfig.elementsSize +
                  gameConfig.columnElementsGap;

                cell.x =
                  x +
                  gameConfig.elementsSize +
                  gameConfig.elementsSize *
                  Math.random() *
                  (gameConfig.elementsByRow / 2 - 2);
                cell.y = y;

                break;

              case 'bottom':
                x = cellIndex * gameConfig.elementsSize;
                y =
                  container.current.clientHeight +
                  rowIndex * gameConfig.elementsSize;

                cell.x = x;
                cell.y =
                  y -
                  gameConfig.elementsSize * 2 -
                  gameConfig.elementsSize *
                  Math.random() *
                  (gameConfig.elementsByColumn / 2 - 2);

                break;

              case 'right':
                x =
                  container.current.clientWidth +
                  rowIndex * gameConfig.elementsSize;
                y =
                  cellIndex * gameConfig.elementsSize +
                  gameConfig.columnElementsGap;

                cell.x =
                  x -
                  gameConfig.elementsSize * 2 -
                  gameConfig.elementsSize *
                  Math.random() *
                  (gameConfig.elementsByRow / 2 - 2);
                cell.y = y;

                break;

              default:
                break;
            }

            cell.id = `element-${side}-${rowIndex}-${cellIndex}-${Date.now()}`;
            cell.direction = side;

            return cell;
          }
        }
        return undefined;
      });
    });
    return { ...config, rows: rewritedElements };
  }

  // Генерация строки случайных элементов (!дополнить типами элементов)
  function newLineGenerating(length) {
    function createRandomElement(stack) {
      const randomNumber = Math.floor(Math.random() * 100);
      if (randomNumber >= 0 && randomNumber < 5) {
        return {
          type: 'box',
          stacks: stack,
          size: gameConfig.elementsSize,
        };
      } else if (randomNumber >= 5 && randomNumber < 7) {
        const randomTriangleAngleType = Math.floor(Math.random() * 4);
        switch (randomTriangleAngleType) {
          case 0:
            return {
              type: 'triangle',
              mainAngle: 'RB',
              stacks: stack,
              size: gameConfig.elementsSize,
            };
          case 1:
            return {
              type: 'triangle',
              mainAngle: 'LB',
              stacks: stack,
              size: gameConfig.elementsSize,
            };
          case 2:
            return {
              type: 'triangle',
              mainAngle: 'RT',
              stacks: stack,
              size: gameConfig.elementsSize,
            };
          case 3:
            return {
              type: 'triangle',
              mainAngle: 'LT',
              stacks: stack,
              size: gameConfig.elementsSize,
            };
          default:
            return undefined;
        }
      } else if (randomNumber >= 7 && randomNumber < 9) {
        const maxBonusesValue = elementsStacksState.current > 5 ? 5 : 4;
        const randomBonusType = Math.floor(Math.random() * maxBonusesValue);

        switch (randomBonusType) {
          case 0:
            return {
              type: 'fire',
              stacks: 5,
              size: gameConfig.elementsSize,
            };
          case 1:
            return {
              type: 'laser',
              stacks: 5,
              size: gameConfig.elementsSize,
            };
          case 2:
            return {
              type: 'multi-gun',
              stacks: 5,
              size: gameConfig.elementsSize,
            };
          case 3:
            return {
              type: 'splash',
              stacks: 5,
              size: gameConfig.elementsSize,
            };
          case 4:
            return {
              type: 'clock',
              stacks: 5,
              size: gameConfig.elementsSize,
              active: false,
            };
          default:
            return undefined;
        }
      } else if (randomNumber >= 9 && randomNumber < 100) {
        return;
      } else {
        return undefined;
      }
    }
    const newLine = {
      rows: [[]],
    };
    for (let i = 0; i < length; i++) {
      const newStack =
        elementsStacksState.current +
        Math.floor(Math.random() * elementsStacksRangeState.current);

      newLine.rows[0].push(createRandomElement(newStack));
    }
    return newLine;
  }

  const stacksIntervalLastTime = useRef(0);
  const stacksIntervalAccumulatedTime = useRef(0);
  const stacksIntervalRangeLastTime = useRef(0);
  const stacksIntervalRangeAccumulatedTime = useRef(0);
  const stacksIntervalExtraTimescape = useRef(1);
  const isLoopStarted = useRef(false);
  const openedTabGap = useRef(0);

  const animationId = useRef(null); // ID для cancelAnimationFrame

  // Функция интервала увеличения стаков
  function intervalLoop(timestamp) {
    if (gameConfig.isBallsMovingStarted) {
      if (!isLoopStarted.current) {
        openedTabGap.current = timestamp;
        isLoopStarted.current = true;
      }
      if (isGamePaused.current) {
        stacksIntervalLastTime.current = timestamp - openedTabGap.current; // Обновляем stacksIntervalLastTime.current, чтобы не учитывать время паузы
        stacksIntervalRangeLastTime.current = timestamp - openedTabGap.current; // Обновляем stacksIntervalRangeLastTime.current, чтобы не учитывать время паузы
      } else {
        stacksIntervalAccumulatedTime.current +=
          timestamp - openedTabGap.current - stacksIntervalLastTime.current;
        stacksIntervalRangeAccumulatedTime.current +=
          timestamp -
          openedTabGap.current -
          stacksIntervalRangeLastTime.current;
        if (
          stacksIntervalAccumulatedTime.current >=
          STACKS_INTERVAL_DURATION * stacksIntervalExtraTimescape.current
        ) {
          updateStacks(); // Запускаем основное действие интервала
          stacksIntervalAccumulatedTime.current -=
            STACKS_INTERVAL_DURATION * stacksIntervalExtraTimescape.current; // Переносим оставшееся время на следующий цикл
        }
        if (
          stacksIntervalRangeAccumulatedTime.current >=
          1000 *
          ELEMENTS_SPEED *
          (40 / stacksIntervalExtraTimescape.current) &&
          elementsStacksRangeState.current < 20
        ) {
          updateStacksRange(); // Запускаем основное действие интервала
          stacksIntervalRangeAccumulatedTime.current -=
            1000 * ELEMENTS_SPEED * (40 / stacksIntervalExtraTimescape.current); // Переносим оставшееся время на следующий цикл
        }
      }
      stacksIntervalLastTime.current = timestamp - openedTabGap.current;
      stacksIntervalRangeLastTime.current = timestamp - openedTabGap.current;
      requestAnimationFrame(intervalLoop); // Перезапуск цикла
    }
  }

  // Функция для повышения стаков блоков
  function updateStacks() {
    elementsStacksState.current += 1;
    if (elementsStacksState.current % 5 === 0) {
      stacksIntervalExtraTimescape.current += 0.5;
    }
  }
  // Функция для повышения разброса стаков
  function updateStacksRange() {
    elementsStacksRangeState.current += 1;
  }

  useEffect(() => {
    if (clockBonusList.some((clockElement) => clockElement.active === false)) {
      const clocksList = [...clockBonusList];
      clocksList.forEach((clockElement) => {
        if (!clockElement.active) {
          updateClockTimer(clockElement.id, clockElement.time);
          clockElement.active = true;
        }
      });
      setClockBonusList(clocksList);
    }

    if (!clockBonusList.some((clockElement) => clockElement.active === true)) {
      isElementsPaused.current = false;
    }
  }, [clockBonusList]);

  function updateClockTimer(id, time) {
    isElementsPaused.current = true;
    const currentClockInterval = setInterval(() => {
      setElementsListState((prevState) => {
        const newRows = prevState.rows.map((row) =>
          row.map((currentElement) => {
            if (currentElement && currentElement.id === id) {
              const newTimer = currentElement.timer - 1;
              currentElement = { ...currentElement, timer: newTimer };
              return currentElement;
            }
            return currentElement;
          })
        );
        return {
          rows: newRows,
        };
      });
    }, 1000);
    setTimeout(() => {
      clearInterval(currentClockInterval);
      setElementsListState((prevState) => {
        const newRows = prevState.rows.map((row) =>
          row.map((currentElement) => {
            if (currentElement && currentElement.id === id) {
              return undefined;
            }
            return currentElement;
          })
        );
        return {
          rows: newRows,
        };
      });
      setClockBonusList((prevState) =>
        prevState.filter((clock) => clock.id !== id)
      );
    }, 1000 * time);
  }

  const currentLaserTimer = useRef(0);
  const currentMultiGunTimer = useRef(0);

  useEffect(() => {
    if (laserBonusTimer.some((laserElement) => laserElement.active === false)) {
      const laserList = [...laserBonusTimer];
      laserList.forEach((laserElement) => {
        if (!laserElement.active) {
          updateLaserBonusTimer(laserElement.id, laserElement.timer);
          isLaserBonusActive.current = true;
          laserElement.active = true;
        }
      });

      setLaserBonusTimer(laserList);
    }

    if (!laserBonusTimer.some((laserElement) => laserElement.active === true)) {
      isLaserBonusActive.current = false;
    } else {
      const filteredTimersList = laserBonusTimer.filter(
        (laser) => laser.active === true
      );
      const currentMaxValue =
        filteredTimersList[filteredTimersList.length - 1].timer;
      currentLaserTimer.current = currentMaxValue;
      if (currentMaxValue === 1) {
        setTimeout(() => {
          currentLaserTimer.current = 0;
        }, 1000);
      }
    }
  }, [laserBonusTimer]);

  function updateLaserBonusTimer(id, time) {
    const currentLaserInterval = setInterval(() => {
      setLaserBonusTimer((prevState) => {
        const newState = prevState.map((currentElement) => {
          if (currentElement && currentElement.id === id) {
            const newTimer = currentElement.timer - 1;
            currentElement = { ...currentElement, timer: newTimer };
          }
          return currentElement;
        });
        return newState;
      });
    }, 1000);
    setTimeout(() => {
      clearInterval(currentLaserInterval);
      setLaserBonusTimer((prevState) =>
        prevState.filter((laser) => laser.id !== id)
      );
    }, 1000 * time);
  }

  const isMultiGunActive = useRef(false);

  useEffect(() => {
    if (
      multiGunBonusTimer.some((laserElement) => laserElement.active === false)
    ) {
      const laserList = [...multiGunBonusTimer];
      laserList.forEach((laserElement) => {
        if (!laserElement.active) {
          updateMultiGunBonusTimer(laserElement.id, laserElement.timer);
          laserElement.active = true;
          isMultiGunActive.current = true;
        }
      });

      setMultiGunBonusTimer(laserList);
    }

    if (
      !multiGunBonusTimer.some((laserElement) => laserElement.active === true)
    ) {
    } else {
      const filteredTimersList = multiGunBonusTimer.filter(
        (laser) => laser.active === true
      );
      const currentMaxValue =
        filteredTimersList[filteredTimersList.length - 1].timer;
      currentMultiGunTimer.current = currentMaxValue;
      if (currentMaxValue === 1) {
        setTimeout(() => {
          currentMultiGunTimer.current = 0;
          isMultiGunActive.current = false;
        }, 1000);
      }
    }
  }, [multiGunBonusTimer]);

  function updateMultiGunBonusTimer(id, time) {
    const currentLaserInterval = setInterval(() => {
      setMultiGunBonusTimer((prevState) => {
        const newState = prevState.map((currentElement) => {
          if (currentElement && currentElement.id === id) {
            const newTimer = currentElement.timer - 1;
            currentElement = { ...currentElement, timer: newTimer };
          }
          return currentElement;
        });
        return newState;
      });
    }, 1000);
    setTimeout(() => {
      clearInterval(currentLaserInterval);
      setMultiGunBonusTimer((prevState) =>
        prevState.filter((laser) => laser.id !== id)
      );
    }, 1000 * time);
  }

  const laserIntervalRef = useRef(null);

  function calculateLaserEndPosition(startX, startY, targetX, targetY) {
    const containerRect = container.current.getBoundingClientRect();
    const deltaX = targetX - startX;
    const deltaY = targetY - startY;

    // Угол направления лазера
    const angle = Math.atan2(deltaY, deltaX);

    // Величины изменения координат до пересечения с границами контейнера
    const tanAngle = Math.tan(angle);

    // Вычисляем потенциальные координаты пересечения с каждой границей контейнера
    const rightX = containerRect.width;
    const rightY = startY + (rightX - startX) * tanAngle;

    const leftX = 0;
    const leftY = startY + (leftX - startX) * tanAngle;

    const bottomY = containerRect.height;
    const bottomX = startX + (bottomY - startY) / tanAngle;

    const topY = 0;
    const topX = startX + (topY - startY) / tanAngle;

    // Определяем конец луча как ближайшее пересечение с границами контейнера
    let endX = startX;
    let endY = startY;

    if (deltaX > 0 && rightY >= 0 && rightY <= containerRect.height) {
      // Пересечение с правой границей
      endX = rightX;
      endY = rightY;
    } else if (deltaX < 0 && leftY >= 0 && leftY <= containerRect.height) {
      // Пересечение с левой границей
      endX = leftX;
      endY = leftY;
    } else if (deltaY > 0 && bottomX >= 0 && bottomX <= containerRect.width) {
      // Пересечение с нижней границей
      endX = bottomX;
      endY = bottomY;
    } else if (deltaY < 0 && topX >= 0 && topX <= containerRect.width) {
      // Пересечение с верхней границей
      endX = topX;
      endY = topY;
    }

    return { endX, endY };
  }

  function drawLaser(startX, startY, endX, endY, currentLaserRef) {
    if (isNaN(firstGunPosition.left)) return;

    stopLaserBeam(currentLaserRef);

    const deltaX = endX - startX;
    const deltaY = endY - startY;
    const distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY);

    let velX = (deltaX / distance) * 8;
    let velY = (deltaY / distance) * 8;

    currentLaserRef.current = document.createElement('div');
    currentLaserRef.current.classList.add('laser-trajectory');
    currentLaserRef.current.style.zIndex = 10;

    let currentX = startX;
    let currentY = startY;
    let currentDistance = 0;

    let forceStopDrawing = false;

    // Множитель является определителем длинны траектории
    while (
      currentDistance < container.current.clientHeight * 1 &&
      !forceStopDrawing
    ) {
      currentX += velX;
      currentY += velY;
      currentDistance += Math.sqrt(
        (currentX - startX) ** 2 + (currentY - startY) ** 2
      );

      if (currentX <= 0 || currentX >= container.current.clientWidth) {
        velX *= -1;
        currentX = Math.min(
          Math.max(currentX, 0),
          container.current.clientWidth
        );
        forceStopDrawing = true;
      }

      if (currentY <= 0 || currentY >= container.current.clientHeight) {
        velY *= -1;
        currentY = Math.min(
          Math.max(currentY, 0),
          container.current.clientHeight
        );
        forceStopDrawing = true;
      }

      const segment = createLaserTrajectorySegment(
        startX,
        startY,
        currentX,
        currentY
      );

      currentLaserRef.current.appendChild(segment);
      startX = currentX;
      startY = currentY;
    }

    container.current.appendChild(currentLaserRef.current);
  }

  function createLaserTrajectorySegment(x1, y1, x2, y2) {
    const segment = document.createElement('div');
    segment.classList.add('laser-trajectory-segment');

    const length = Math.sqrt((x2 - x1) ** 2 + (y2 - y1) ** 2);
    const angle = (Math.atan2(y2 - y1, x2 - x1) * 180) / Math.PI;

    segment.style.width = `${length}px`;
    segment.style.left = 0 - length / 2 - gameConfig.ballsSize / 4 + 'px';
    segment.style.transform = `translate(${x1}px, ${y1}px) rotate(${angle}deg)`;

    return segment;
  }

  function checkLaserIntersections(startX, startY, endX, endY) {
    const elements = container.current.querySelectorAll('.element');

    elements.forEach((element) => {
      const rect = element.getBoundingClientRect();
      const containerRect = container.current.getBoundingClientRect();
      const elementCorners = [
        { x: rect.left - containerRect.left, y: rect.top - containerRect.top }, // верхний левый угол
        { x: rect.right - containerRect.left, y: rect.top - containerRect.top }, // верхний правый угол
        {
          x: rect.right - containerRect.left,
          y: rect.bottom - containerRect.top,
        }, // нижний правый угол
        {
          x: rect.left - containerRect.left,
          y: rect.bottom - containerRect.top,
        }, // нижний левый угол
      ];

      // Проверка пересечения лазера с каждой стороной прямоугольника элемента
      const intersects =
        lineIntersects(
          startX,
          startY,
          endX,
          endY,
          elementCorners[0],
          elementCorners[1]
        ) || // верхняя сторона
        lineIntersects(
          startX,
          startY,
          endX,
          endY,
          elementCorners[1],
          elementCorners[2]
        ) || // правая сторона
        lineIntersects(
          startX,
          startY,
          endX,
          endY,
          elementCorners[2],
          elementCorners[3]
        ) || // нижняя сторона
        lineIntersects(
          startX,
          startY,
          endX,
          endY,
          elementCorners[3],
          elementCorners[0]
        ); // левая сторона

      if (intersects) {
        decreaseStacks(element, 1, false); // Снижает стеки при пересечении
        checkBonusEffect(element, 1, false);
      }
    });
  }

  // Функция для проверки пересечения двух отрезков (лазер и сторона элемента)
  function lineIntersects(x1, y1, x2, y2, p1, p2) {
    const det = (x2 - x1) * (p2.y - p1.y) - (y2 - y1) * (p2.x - p1.x);
    if (det === 0) return false; // Линии параллельны

    const lambda =
      ((p2.y - p1.y) * (p2.x - x1) + (p1.x - p2.x) * (p2.y - y1)) / det;
    const gamma = ((y1 - y2) * (p2.x - x1) + (x2 - x1) * (p2.y - y1)) / det;

    return 0 <= lambda && lambda <= 1 && 0 <= gamma && gamma <= 1;
  }

  // Функция для запуска лазера
  function startLaserBeam() {
    if (isAnimating || isGameOver) return;

    const startX = firstGunPosition.left + gameConfig.ballsSize / 2;
    const startY = firstGunPosition.top + gameConfig.ballsSize / 2;

    const endX =
      clientXRef.current -
      container.current.getBoundingClientRect().left +
      gameConfig.ballsSize / 2;
    const endY =
      clientYRef.current -
      container.current.getBoundingClientRect().top +
      gameConfig.ballsSize / 2;

    drawLaser(startX, startY, endX, endY, trajectoryLaserElement);

    if (isMultiGunActive.current) {
      // Конечная точка по текущей позиции (до поворота)
      const targetX =
        clientXRef.current -
        container.current.getBoundingClientRect().left +
        gameConfig.ballsSize / 2;
      const targetY =
        clientYRef.current -
        container.current.getBoundingClientRect().top +
        gameConfig.ballsSize / 2;

      // Вычисляем вектор направления до цели
      const deltaX = targetX - startX;
      const deltaY = targetY - startY;

      // Поворачиваем вектор на 60 градусов
      const angle2 = degToRad(60); // Конвертируем 60 градусов в радианы
      const { newVelX: newVelX2, newVelY: newVelY2 } = rotateVector(
        deltaX,
        deltaY,
        angle2
      );

      // Вычисляем конечные координаты лазера на границе контейнера
      const { endX: endX2, endY: endY2 } = calculateLaserEndPosition(
        startX,
        startY,
        startX + newVelX2,
        startY + newVelY2
      );
      console.log(deltaX, deltaY, angle2);

      console.log(angle2, newVelX2, newVelY2, endX2, endY2);
      // Поворачиваем вектор на 60 градусов
      const angle3 = degToRad(-60); // Конвертируем 60 градусов в радианы
      const { newVelX: newVelX3, newVelY: newVelY3 } = rotateVector(
        deltaX,
        deltaY,
        angle3
      );

      // Вычисляем конечные координаты лазера на границе контейнера
      const { endX: endX3, endY: endY3 } = calculateLaserEndPosition(
        startX,
        startY,
        startX + newVelX3,
        startY + newVelY3,
        container.current
      );
      // Поворачиваем вектор на 60 градусов
      const angle4 = degToRad(120); // Конвертируем 60 градусов в радианы
      const { newVelX: newVelX4, newVelY: newVelY4 } = rotateVector(
        deltaX,
        deltaY,
        angle4
      );

      // Вычисляем конечные координаты лазера на границе контейнера
      const { endX: endX4, endY: endY4 } = calculateLaserEndPosition(
        startX,
        startY,
        startX + newVelX4,
        startY + newVelY4,
        container.current
      );
      // Поворачиваем вектор на 60 градусов
      const angle5 = degToRad(-120); // Конвертируем 60 градусов в радианы
      const { newVelX: newVelX5, newVelY: newVelY5 } = rotateVector(
        deltaX,
        deltaY,
        angle5
      );

      // Вычисляем конечные координаты лазера на границе контейнера
      const { endX: endX5, endY: endY5 } = calculateLaserEndPosition(
        startX,
        startY,
        startX + newVelX5,
        startY + newVelY5,
        container.current
      );
      // Поворачиваем вектор на 60 градусов
      const angle6 = degToRad(180); // Конвертируем 60 градусов в радианы
      const { newVelX: newVelX6, newVelY: newVelY6 } = rotateVector(
        deltaX,
        deltaY,
        angle6
      );

      // Вычисляем конечные координаты лазера на границе контейнера
      const { endX: endX6, endY: endY6 } = calculateLaserEndPosition(
        startX,
        startY,
        startX + newVelX6,
        startY + newVelY6,
        container.current
      );

      // Рисуем лазер с повернутым направлением
      drawLaser(startX, startY, endX2, endY2, trajectoryLaserElement2);
      drawLaser(startX, startY, endX3, endY3, trajectoryLaserElement3);
      drawLaser(startX, startY, endX4, endY4, trajectoryLaserElement4);
      drawLaser(startX, startY, endX5, endY5, trajectoryLaserElement5);
      drawLaser(startX, startY, endX6, endY6, trajectoryLaserElement6);
    }
  }

  function updateLaserBeam(event) {
    const { clientX, clientY } = event;
    clientXRef.current = clientX;
    clientYRef.current = clientY;
    const startX = firstGunPosition.left + gameConfig.ballsSize / 2;
    const startY = firstGunPosition.top + gameConfig.ballsSize / 2;
    const endX =
      clientX -
      container.current.getBoundingClientRect().left +
      gameConfig.ballsSize / 2;
    const endY =
      clientY -
      container.current.getBoundingClientRect().top +
      gameConfig.ballsSize / 2;

    drawLaser(startX, startY, endX, endY, trajectoryLaserElement);

    if (isMultiGunActive.current) {
      // Конечная точка по текущей позиции (до поворота)
      const targetX =
        clientX -
        container.current.getBoundingClientRect().left +
        gameConfig.ballsSize / 2;
      const targetY =
        clientY -
        container.current.getBoundingClientRect().top +
        gameConfig.ballsSize / 2;

      // Вычисляем вектор направления до цели
      const deltaX = targetX - startX;
      const deltaY = targetY - startY;

      // Поворачиваем вектор на 60 градусов
      const angle2 = degToRad(60); // Конвертируем 60 градусов в радианы
      const { newVelX: newVelX2, newVelY: newVelY2 } = rotateVector(
        deltaX,
        deltaY,
        angle2
      );

      // Вычисляем конечные координаты лазера на границе контейнера
      const { endX: endX2, endY: endY2 } = calculateLaserEndPosition(
        startX,
        startY,
        startX + newVelX2,
        startY + newVelY2
      );
      // Поворачиваем вектор на 60 градусов
      const angle3 = degToRad(-60); // Конвертируем 60 градусов в радианы
      const { newVelX: newVelX3, newVelY: newVelY3 } = rotateVector(
        deltaX,
        deltaY,
        angle3
      );

      // Вычисляем конечные координаты лазера на границе контейнера
      const { endX: endX3, endY: endY3 } = calculateLaserEndPosition(
        startX,
        startY,
        startX + newVelX3,
        startY + newVelY3,
        container.current
      );
      // Поворачиваем вектор на 60 градусов
      const angle4 = degToRad(120); // Конвертируем 60 градусов в радианы
      const { newVelX: newVelX4, newVelY: newVelY4 } = rotateVector(
        deltaX,
        deltaY,
        angle4
      );

      // Вычисляем конечные координаты лазера на границе контейнера
      const { endX: endX4, endY: endY4 } = calculateLaserEndPosition(
        startX,
        startY,
        startX + newVelX4,
        startY + newVelY4,
        container.current
      );
      // Поворачиваем вектор на 60 градусов
      const angle5 = degToRad(-120); // Конвертируем 60 градусов в радианы
      const { newVelX: newVelX5, newVelY: newVelY5 } = rotateVector(
        deltaX,
        deltaY,
        angle5
      );

      // Вычисляем конечные координаты лазера на границе контейнера
      const { endX: endX5, endY: endY5 } = calculateLaserEndPosition(
        startX,
        startY,
        startX + newVelX5,
        startY + newVelY5,
        container.current
      );
      // Поворачиваем вектор на 60 градусов
      const angle6 = degToRad(180); // Конвертируем 60 градусов в радианы
      const { newVelX: newVelX6, newVelY: newVelY6 } = rotateVector(
        deltaX,
        deltaY,
        angle6
      );

      // Вычисляем конечные координаты лазера на границе контейнера
      const { endX: endX6, endY: endY6 } = calculateLaserEndPosition(
        startX,
        startY,
        startX + newVelX6,
        startY + newVelY6,
        container.current
      );

      // Рисуем лазер с повернутым направлением
      drawLaser(startX, startY, endX2, endY2, trajectoryLaserElement2);
      drawLaser(startX, startY, endX3, endY3, trajectoryLaserElement3);
      drawLaser(startX, startY, endX4, endY4, trajectoryLaserElement4);
      drawLaser(startX, startY, endX5, endY5, trajectoryLaserElement5);
      drawLaser(startX, startY, endX6, endY6, trajectoryLaserElement6);
    }
  }

  // Функция для остановки лазера
  function stopLaserBeam(currentLaserRef) {
    if (currentLaserRef && currentLaserRef.current) {
      currentLaserRef.current.remove();
      if (isLaserBonusActive.current) {
        currentLaserRef.current = null;
      }
    }
    if (laserIntervalRef.current) {
      clearInterval(laserIntervalRef.current);
      laserIntervalRef.current = null;
    }
  }

  const gunPlatformRef = useRef(null);

  return (
    <>
      <div className='classic_mode' id='classic_mode'>
        <button onClick={goHomeHandler}>Home</button>
        <button onClick={pauseGame}>Pause</button>
        <div className='exp_container'>
          <span className='exp_value'>{expValue}</span>
        </div>
        <div
          ref={container}
          id='container'
          onMouseDown={startTrajectoryUpdating}
          onMouseMove={isMouseHolded ? mouseMoveHandler : null}
          onMouseUp={stopTrajectoryUpdating}
        >
          {elementsListState.rows.length > 0 ? (
            <Elements rows={elementsListState.rows} />
          ) : null}
          {gameConfig.updated ? (
            <GunPlatform
              x={
                (container.current.clientWidth / 5) * 2 +
                container.current.clientWidth / 10 / 2
              }
              y={
                container.current.clientHeight / 2 -
                container.current.clientWidth / 10 / 2
              }
              size={container.current.clientWidth / 10}
              platformRef={gunPlatformRef}
            />
          ) : null}
          {gameConfig.updated ? (
            <div
              style={{
                width: container.current.clientWidth / 10,
                height: container.current.clientWidth / 20,
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                position: 'absolute',
                zIndex: 1000,
                left:
                  (container.current.clientWidth / 5) * 2 +
                  container.current.clientWidth / 10 / 2,
                top:
                  container.current.clientHeight / 2 -
                  container.current.clientWidth / 10 / 2,
              }}
            >
              <span>
                {currentMultiGunTimer.current > 0 ||
                  currentLaserTimer.current > 0
                  ? `${currentLaserTimer.current > currentMultiGunTimer.current
                    ? currentLaserTimer.current
                    : currentMultiGunTimer.current
                  } s`
                  : null}
              </span>
            </div>
          ) : null}
          {gameConfig && container.current && isGun1Active ? (
            <div
              ref={gun1Element}
              style={{
                width: container.current.clientWidth / 10,
                height: container.current.clientWidth / 10,
                top:
                  firstGunPosition.top -
                  container.current.clientWidth / 10 +
                  gameConfig.ballsSize * 1.1,
                left:
                  (container.current.clientWidth / 5) * 2 +
                  container.current.clientWidth / 10 / 2,
              }}
              className='gunElement'
            />
          ) : null}
          <div className='bonuses-panel' style={{ position: 'absolute' }}>
            <button
              onClick={() => {
                if (multiGunCounter > 0) {
                  setMultiGunBonusTimer((prevState) => [
                    ...prevState,
                    { id: `multi-gun-${Date.now()}`, active: false, timer: 10 },
                  ]);
                  setMultiGunCounter((prevState) => prevState - 1);
                }
              }}
            >
              {multiGunCounter}/3
            </button>
            <button
              onClick={() => {
                if (splashCounter > 0) {
                  createLaserContainer(
                    container.current.clientWidth / 6,
                    container.current.clientHeight / 6,
                    container.current.clientWidth / 1.5,
                    container.current.clientHeight / 1.5,
                    1,
                    true
                  );
                  setSplashCounter((prevState) => prevState - 1);
                }
              }}
            >
              {splashCounter}/3
            </button>
          </div>
        </div>
      </div>
    </>
  );
}
