export default function AddBalls(props) {
  const { x, y, bonus, line, size, id } = props;
  return (
    <div
      id={id}
      className='element bonus-box add-balls-bonus'
      data-extra-balls={bonus}
      data-line={line}
      style={{
        left: `${x}px`,
        top: `${y}px`,
        width: `${size}px`,
        height: `${size}px`,
      }}
    >
      <div className="circle-bonus bonus-balls-circle">
        <span style={{fontSize:  `${size/3}px`}}>+{bonus}</span>
      </div>
    </div>
  );
}
