import './StorePopup.css';
import storeXMark from '../../pages/main/sources/store-cross-btn.png';

export default function StorePopup(props) {
  const { closeFunc } = props;
  return (
    <div className='store_popup_tab popup_tab'>
      <div className='store_popup_container popup_container'>
        <button
          className='close_tab_btn'
          onClick={closeFunc}
          style={{
            backgroundImage: `ulr('${storeXMark}')`,
            backgroundSize: 'cover',
          }}
        >
          <img src={storeXMark} alt="x" />
        </button>
        <div className="store_popup-title popup-title">
          <span className='absolute_text'>store</span>
        </div>
      </div>
    </div>
  );
}
