import { useSelector, useDispatch } from "react-redux";
import "./DailyTasksList.css";
import DailyTaskView from "../DailyTaskView/DailyTaskView";
import { claimTaskReward } from "../../../../../features/slices/dailyTasksSlice";
import { dailyTasksData } from "../../../../../features/dailyTasks/dailyTasksData";
import { useLocalization } from "../../../../../features/localization/localizationHook";
import { useEffect, useState } from "react";

const DailyTasksList = () => {
    const statistics = useSelector((state) => state.user.statistics);
    const rewardsClaimed = useSelector((state) => state.tasks.rewardsClaimed);
    const dispatch = useDispatch();

    const { title, taskCompletion } = useLocalization({
        title: 'dailyTasks',
        taskCompletion: 'taskCompleted'
    }) || {};

    const [tasks, setTasks] = useState(null);

    useEffect(() => {
        setTasks(null);

        let object = {};
        Object.keys(dailyTasksData).forEach((key) => {
            const task = dailyTasksData[key];
            if (task.length > 0) {
                task.map((subTask, index) => {
                    const completed = rewardsClaimed[key][index].state;
                    if (!completed) {
                        object[key] = task;
                        setTasks(object);
                    }
                })
            } else {
                const completed = rewardsClaimed[key];
                if (!completed) {
                    object[key] = task;
                    setTasks(object);
                }
            }
        });
    }, [rewardsClaimed]);

    const handleClaim = (key, type) => {
        dispatch(claimTaskReward({ key, type }));
    };

    return (
        <div className="daily-tasks-list">
            <h2>{title}</h2>
            {tasks != null ? Object.keys(tasks).map((key) => {
                const task = tasks[key];
                if (task.length > 0) {
                    return task.map((value, i) => {
                        const type = task[i].type;
                        const elementKey = key + type;
                        const taskStruct = task[i].task;
                        const progressionStruct = statistics[key][i].count;
                        const rewardCaimedStruct = rewardsClaimed[key][i].state;
                        return <DailyTaskView
                            key={elementKey}
                            taskKey={key}
                            type={type}
                            task={taskStruct}
                            progress={progressionStruct}
                            claimed={rewardCaimedStruct}
                            onClaim={handleClaim}
                        />
                    });
                } else {
                    return <DailyTaskView
                        key={key}
                        taskKey={key}
                        task={task}
                        progress={statistics[key]}
                        claimed={rewardsClaimed[key]}
                        onClaim={handleClaim}
                    />
                }
            }) : <p>{taskCompletion}</p>}
        </div>
    );
};

export default DailyTasksList;
