import './DailyTasksPage.css';
import { useDispatch } from "react-redux";
import { addBlocksDestroyed, addStarsCount, addWinCount } from "../../../features/slices/userSlice";
import DailyTasksList from './subComponents/DailyTasksList/DailyTasksList';

export default function DailyTasksPage() {

    const dispatch = useDispatch();

    // <test>
    const win = () => {
        dispatch(addWinCount(1));
    }
    const destroy = () => {
        dispatch(addBlocksDestroyed({ count: 1 }));
    }
    const addStars = () => {
        dispatch(addStarsCount(1));
    }

    // </test>

    return <div className="body"><DailyTasksList />
        <button className="button" onClick={win}>WIN</button>
        <button className="button" onClick={destroy}>DESTROY</button>
        <button className="button" onClick={addStars}>ADD STAR</button>
    </div>
}