import AddBalls from '../elementTypes/AddBalls';
import BallDestroyerBox from '../elementTypes/BallDestroyerBox';
import BallDestroyerTrap from '../elementTypes/BallDestroyerTrap';
import Box from '../elementTypes/Box';
import FireBall from '../elementTypes/FireBall';
import FixedTrajectoryChange from '../elementTypes/FixedTrajectoryChange';
import Laser from '../elementTypes/Laser';
import MagneticTrapBox from '../elementTypes/MagneticTrapBox';
import MetalBox from '../elementTypes/MetalBox';
import MirrorTrajectoryChange from '../elementTypes/MirrorTrajectoryChange';
import RandomTrajectoryChange from '../elementTypes/RandomTrajectoryChange';
import RocketBox from '../elementTypes/RocketBox';
import Teleport from '../elementTypes/Teleport';
import Triangle from '../elementTypes/Triangle';
import UntouchableBox from '../elementTypes/UntouchableBox';

export default function ElementsLine(props) {
  const { row, line } = props;
  const createdElements = row.map((element, index) => {
    if (element) {
      switch (element.type) {
        case 'box':
          return (
            <Box
              key={index}
              x={element.x}
              y={element.y}
              line={line}
              stacks={element.stacks}
              size={element.size}
              id={element.id}
            />
          );
        case 'untouchableBox':
          return (
            <UntouchableBox
              key={index}
              x={element.x}
              y={element.y}
              line={line}
              stacks={element.stacks}
              size={element.size}
              id={element.id}
            />
          );
        case 'magneticTrapBox':
          return (
            <MagneticTrapBox
              key={index}
              x={element.x}
              y={element.y}
              line={line}
              stacks={element.stacks}
              range={element.range}
              gravityScale={element.gravityScale}
              size={element.size}
              id={element.id}
            />
          );

        case 'triangle':
          return (
            <Triangle
              key={index}
              x={element.x}
              y={element.y}
              mainAngle={element.mainAngle}
              line={line}
              stacks={element.stacks}
              size={element.size}
              id={element.id}
            />
          );
        case 'rocketBox':
          return (
            <RocketBox
              key={index}
              x={element.x}
              y={element.y}
              rockets={element.rockets}
              line={line}
              stacks={element.stacks}
              size={element.size}
              id={element.id}
            />
          );
        case 'ballDestroyerBox':
          return (
            <BallDestroyerBox
              key={index}
              x={element.x}
              y={element.y}
              line={line}
              stacks={element.stacks}
              size={element.size}
              id={element.id}
            />
          );

        case 'addBalls':
          return (
            <AddBalls
              key={index}
              x={element.x}
              y={element.y}
              bonus={element.bonus}
              line={line}
              size={element.size}
              id={element.id}
            />
          );

          case 'laser':
            return (
              <Laser
                key={index}
                x={element.x}
                y={element.y}
                direction={element.direction}
                line={line}
                size={element.size}
                id={element.id}
              />
            );
          case 'fireBall':
            return (
              <FireBall
                key={index}
                x={element.x}
                y={element.y}
                damage={element.damage}
                line={line}
                size={element.size}
                id={element.id}
              />
            );
          case 'ballDestroyerTrap':
            return (
              <BallDestroyerTrap
                key={index}
                x={element.x}
                y={element.y}
                line={line}
                size={element.size}
                id={element.id}
              />
            );
          case 'randomTrajectoryChange':
            return (
              <RandomTrajectoryChange
                key={index}
                x={element.x}
                y={element.y}
                line={line}
                size={element.size}
                id={element.id}
              />
            );
          case 'mirrorTrajectoryChange':
            return (
              <MirrorTrajectoryChange
                key={index}
                x={element.x}
                y={element.y}
                line={line}
                size={element.size}
                id={element.id}
              />
            );
          case 'fixedTrajectoryChange':
            return (
              <FixedTrajectoryChange
                key={index}
                x={element.x}
                y={element.y}
                direction={element.direction}
                line={line}
                size={element.size}
                id={element.id}
              />
            );
          case 'teleport':
            return (
              <Teleport
                key={index}
                x={element.x}
                y={element.y}
                refName={element.refName}
                name={element.name}
                line={line}
                size={element.size}
                id={element.id}
              />
            );
          case 'metalBox':
            return (
              <MetalBox
                key={index}
                x={element.x}
                y={element.y}
                size={element.size}
              />
            );

        default:
          return undefined;
      }
    } else return undefined
  });
  return createdElements;
}
