import { configureStore } from '@reduxjs/toolkit';
import userReducer from '../features/slices/userSlice.js';
import achievementsReducer from '../features/slices/achievementsSlice.js';
import tasksReducer from '../features/slices/dailyTasksSlice.js';
import localizationReducer from '../features/slices/localizationSlice.js';

export const store = configureStore({
  reducer: {
    user: userReducer,
    achievements: achievementsReducer,
    tasks: tasksReducer,
    localization: localizationReducer
  },
});
