import { useSelector } from "react-redux";
import { achievements } from "../../../../../features/achievements/achievementsData.js";
import { AchievementView } from "../AchievementView/AchievementView.jsx";
import "./AchievementsList.css";
import { useLocalization } from "../../../../../features/localization/localizationHook.js";

export const AchievementsList = () => {
    const progress = useSelector((state) => state.achievements.achievementProgress);
    const statistics = useSelector((state) => state.user.statistics);

    const { title } = useLocalization({
        title: "achievements"
    }) || {};

    return (
        <div className="achievements-list">
            <h2>{title}</h2>
            <div className="achievements-container">
                {
                    Object.keys(achievements).map((key) => {
                        const achievement = achievements[key];

                        if (achievement.length > 0) {
                            return achievement.map((val, i) => {
                                const type = achievement[i].type;
                                const elementKey = key + type;
                                const activementStruct = achievement[i].achievement;
                                const statisticsElement = statistics[key][i].count;
                                const progressElement = progress[key][i].progress;

                                return <AchievementView
                                    key={elementKey}
                                    type={type}
                                    achievementKey={key}
                                    achievement={activementStruct}
                                    progress={statisticsElement}
                                    level={progressElement}
                                />
                            })
                        } else {
                            return <AchievementView
                                key={key}
                                achievementKey={key}
                                achievement={achievements[key]}
                                progress={statistics[key]}
                                level={progress[key]}
                            />
                        }
                    })
                }
            </div>
        </div>
    );
};

