import Box from '../elementTypes/Box';
import ExtraBall from '../elementTypes/ExtraBall';
import ExtraDamage from '../elementTypes/ExtraDamage';
import Triangle from '../elementTypes/Triangle';

export default function ElementsLine(props) {
  const { row, line } = props;
  const createdElements = row.map((element, index) => {
    if (element) {
      switch (element.type) {
        case 'box':
          return (
            <Box
              key={index}
              x={element.x}
              y={element.y}
              line={line}
              stacks={element.stacks}
              size={element.size}
              id={element.id}
            />
          );

        case 'triangle':
          return (
            <Triangle
              key={index}
              x={element.x}
              y={element.y}
              mainAngle={element.mainAngle}
              line={line}
              stacks={element.stacks}
              size={element.size}
              id={element.id}
            />
          );

        case 'extraBall':
          return (
            <ExtraBall
              key={index}
              x={element.x}
              y={element.y}
              line={line}
              size={element.size}
              id={element.id}
            />
          );

          case 'extraDamage':
            return (
              <ExtraDamage
                key={index}
                x={element.x}
                y={element.y}
                line={line}
                size={element.size}
                id={element.id}
              />
            );

        default:
          return undefined;
      }
    } else return undefined
  });
  return createdElements;
}
