export default function BallDestroyerTrap(props) {
  const { x, y, line, size, id } = props;
  return (
    <div
      id={id}
      className='element bonus-box ballDestroyerTrap-bonus trap'
      data-activated='false'
      data-line={line}
      style={{
        left: `${x}px`,
        top: `${y}px`,
        width: `${size}px`,
        height: `${size}px`,
      }}
    >
      <div className="circle-bonus ballDestroyerTrap-circle">
        <span style={{fontSize:  `${size/3}px`}}>{'D'}</span>
      </div>
    </div>
  );
}
