import Box from '../elementTypes/Box';
import Clock from '../elementTypes/Clock';
import Fire from '../elementTypes/Fire';
import Laser from '../elementTypes/Laser';
import MultiGun from '../elementTypes/MultiGun';
import Splash from '../elementTypes/Splash';
import Triangle from '../elementTypes/Triangle';

export default function ElementsLine(props) {
  const { row, line } = props;
  const createdElements = row.map((element, index) => {
    if (element) {
      switch (element.type) {
        case 'box':
          return (
            <Box
              key={index}
              x={element.x}
              y={element.y}
              line={line}
              stacks={element.stacks}
              size={element.size}
              id={element.id}
            />
          );

        case 'triangle':
          return (
            <Triangle
              key={index}
              x={element.x}
              y={element.y}
              mainAngle={element.mainAngle}
              line={line}
              stacks={element.stacks}
              size={element.size}
              id={element.id}
            />
          );
        case 'laser':
          return (
            <Laser
              key={index}
              x={element.x}
              y={element.y}
              line={line}
              size={element.size}
              stacks={element.stacks}
              id={element.id}
            />
          );
          case 'fire':
            return (
              <Fire
                key={index}
                x={element.x}
                y={element.y}
                line={line}
                size={element.size}
                stacks={element.stacks}
                id={element.id}
              />
            );
          case 'multi-gun':
            return (
              <MultiGun
                key={index}
                x={element.x}
                y={element.y}
                line={line}
                size={element.size}
                stacks={element.stacks}
                id={element.id}
              />
            );
          case 'splash':
            return (
              <Splash
                key={index}
                x={element.x}
                y={element.y}
                line={line}
                size={element.size}
                stacks={element.stacks}
                id={element.id}
              />
            );
          case 'clock':
            return (
              <Clock
                key={index}
                x={element.x}
                y={element.y}
                line={line}
                size={element.size}
                stacks={element.stacks}
                active={element.active}
                timer={element.active ? element.timer : 0 }
                id={element.id}
              />
            );
        default:
          return undefined;
      }
    } else return undefined
  });
  return createdElements;
}
