import { useEffect, useRef, useState } from 'react';
import { useComponentContext } from '../../contexts/tabTypeContext/TabTypeContext';
import { BALL_SPEED, ELEMENTS_BY_ROW } from './HundredBallsModeConstants';
import './HundredBallsModePage.css';
import Ball from './subComponents/ball/Ball';
import Elements from './subComponents/elements/Elements';
import MetalBox from './subComponents/elements/elementTypes/MetalBox';

export default function HundredBallsModePage() {
  const { setActiveComponent } = useComponentContext();

  const goHomeHandler = () => {
    pageWasClosed.current = true;
    setActiveComponent('MainPage');
  };

  const container = useRef(null);
  const waitingContainer = useRef(null);
  const stoppedContainer = useRef(null);
  const waitingCounter = useRef(null);
  const stoppedCounter = useRef(null);
  const stopButton = useRef(null);
  const scrollControlButton = useRef(null);
  const noActiveGameZonePanel = useRef(null);
  const bonusPanel = useRef(null);
  const scrollPanel = useRef(null);
  const scrollPanelArrowBack = useRef(null);

  const [waitingCounterState, setWaitingCounterState] = useState(0);
  const [stoppedCounterState, setStoppedCounterState] = useState(0);

  const [gameConfig, updateGameConfig] = useState({
    elementsByRow: ELEMENTS_BY_ROW,
    ballSpeed: BALL_SPEED,
    updated: false,
    isInitialBallsCreated: false,
  });

  useEffect(() => {
    updateGameConfig({
      ...gameConfig,
      updated: true,
      ballsSize: container.current.clientWidth / 50,
      elementsSize: container.current.clientWidth / gameConfig.elementsByRow,
      maxLineNumber:
        Math.floor(
          (container.current.clientHeight -
            (container.current.clientWidth / 50) * 2) /
            (container.current.clientWidth / gameConfig.elementsByRow)
        ) - 1,
    });
  }, [container]);

  useEffect(() => {
    setStopPosition({
      left: container.current.offsetWidth / 2 - gameConfig.ballsSize / 2,
      top: container.current.clientHeight - gameConfig.ballsSize * 1.1,
      isUpdated: false,
    });
    setWaitingContainerPosition({
      left: container.current.offsetWidth / 2,
      top: container.current.clientHeight - gameConfig.ballsSize,
      isUpdated: false,
    });
    setStoppedContainerPosition({
      left: container.current.offsetWidth / 2,
      top: container.current.clientHeight - gameConfig.ballsSize,
      isUpdated: false,
    });
    setBallsContainersConfig({
      width: gameConfig.ballsSize,
      height: gameConfig.ballsSize,
      fontSize: gameConfig.elementsSize / 2,
    });
  }, [container, gameConfig]);

  const [elementsStacksState, setElementsStacksState] = useState(1);

  const [ballsContainersConfig, setBallsContainersConfig] = useState({});

  const [stopButtonVisible, setStopButtonVisible] = useState('none');
  const [scrollPanelVisible, setScrollPanelVisible] = useState('none');
  const [bonusesPanelVisible, setBonusesPanelVisible] = useState('flex');
  const [noActiveGameZonePanelVisible, setNoActiveGameZonePanelVisible] =
    useState('flex');

  const [isGameOver, setGameOver] = useState(false);
  const isGamePaused = useRef(false);
  const [animationRunning, setAnimationRunning] = useState(false);

  let isAnimating = false;
  const forceStopBallsMoving = useRef(false);
  const pageWasClosed = useRef(false);
  let balls = [];
  let extraBallsQue = 0;

  const stopAnimation = () => {
    forceStopBallsMoving.current = true;
  };
  const pauseGame = () => {
    isGamePaused.current = !isGamePaused.current;
  };

  const [waitingBalls, setWaitingBalls] = useState([]);

  const [stopPosition, setStopPosition] = useState({});
  const [waitingContainerPosition, setWaitingContainerPosition] = useState({});
  const [stoppedContainerPosition, setStoppedContainerPosition] = useState({});

  useEffect(() => {
    if (stopPosition.top) {
      setWaitingContainerPosition(stopPosition);
      setStoppedContainerPosition(stopPosition);
    }
  }, [stopPosition]);

  // Создание мяча и добавление его в массив мячей
  function createBall(index, forOneUse) {
    const style = {
      width: gameConfig.ballsSize,
      height: gameConfig.ballsSize,
      left: -gameConfig.ballsSize / 2,
    };
    const ballElement = {
      elementKey: index,
      x: 0,
      y: 0,
      velX: 0,
      velY: 0,
      style: style,
      forOneUse: forOneUse,
    };
    return ballElement;
  }

  function createBalls(value, forOneUse = false) {
    const newBalls = [];
    const prevLength = waitingBalls.length;
    for (let i = 0; i < value; i++) {
      newBalls.push(createBall(`ball-${i + prevLength}`, forOneUse));
    }
    setWaitingBalls([...waitingBalls, ...newBalls]);
    setWaitingCounterState(prevLength + value);
  }
  // Множитель бонусной скорости мячей (увеличится со временем)
  const bonusSpeed = useRef(1);

  useEffect(() => {
    if (gameConfig.updated && !gameConfig.isInitialBallsCreated) {
      createBalls(100);
      addNewLineClassicMode(ELEMENTS_BY_ROW);

      gameConfig.isInitialBallsCreated = true;
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [gameConfig.updated]);

  const waitingContainerStyle = {
    width: `${gameConfig.ballsSize}px`,
    height: `${gameConfig.ballsSize}px`,
    left: `${waitingContainerPosition.left}px`,
    top: `${waitingContainerPosition.top}px`,
  };
  const stoppedContainerStyle = {
    width: `${gameConfig.ballsSize}px`,
    height: `${gameConfig.ballsSize}px`,
    left: `${stoppedContainerPosition.left}px`,
    top: `${stoppedContainerPosition.top}px`,
  };
  
  // Счетчик опыта, получаемого за разбитые блоки
  const [expValue, setExpValue] = useState(0);

  // Логика запуска мячей (перенесенная с чистого js)

  function moveBall(ball, startedPosition) {
    let ballStopped = false;
    ball.element = document.getElementById(ball.elementKey);

    // Объявление начальной позиции мяча
    ball.x = startedPosition.left;
    ball.y = startedPosition.top;

    function move() {
      if (ballStopped || pageWasClosed.current) return;
      if (!isGamePaused.current) {
        const ballRect = ball.element.getBoundingClientRect();

        ball.x += ball.velX * bonusSpeed.current;
        ball.y += ball.velY * bonusSpeed.current;

        // Поворот шара в направлении движения
        let angle = Math.atan2(ball.velY, ball.velX) * (180 / Math.PI);
        ball.element.style.transform = `rotate(${angle - 90}deg)`;

        // Проверка столкновения с контейнером
        if (
          ball.x - gameConfig.ballsSize / 2 <= 0 ||
          ball.x - gameConfig.ballsSize / 2 + ballRect.width >=
            container.current.clientWidth
        ) {
          ball.x -= ball.velX * bonusSpeed.current; // Шаг назад
          ball.velX = -ball.velX;
        }

        if (ball.y <= 0) {
          ball.y -= ball.velY * bonusSpeed.current; // Шаг назад
          ball.velY = -ball.velY;
        }

        ball.element.style.left = ball.x - gameConfig.ballsSize / 2 + 'px';
        ball.element.style.top = ball.y + 'px';

        // Проверка столкновения с нижней гранью контейнера + принудительная остановка
        if (
          ball.y + ballRect.height >= container.current.clientHeight ||
          forceStopBallsMoving.current
        ) {
          ballStopped = true;
          ball.element.style.top =
            container.clientHeight - ballRect.height + 'px';
          ball.element.style.transform = 'rotate(0)';
          if (!stopPosition.isUpdated) {
            stopPosition.isUpdated = true;
            stopPosition.left = forceStopBallsMoving.current
              ? stopPosition.left
              : ball.x;
            stopPosition.top = container.current.clientHeight - ballRect.height;

            stoppedContainer.current.style.left =
              stopPosition.left + ballRect.width / 2 + 'px';
            stoppedContainer.current.style.top =
              container.current.clientHeight - ballRect.height + 'px';
            stoppedContainer.current.appendChild(ball.element);

            ball.element.style.left = 0 - gameConfig.ballsSize / 2 + 'px';
            ball.element.style.top = 0;
            balls = balls.filter(
              (filteredBall) => filteredBall.element !== ball.element
            );
          } else {
            stopBallAnimation(ball);
          }
          setTimeout(() => {
            updateStoppedCounter();
            checkAllBallsStopped();
          }, 100);
          return;
        }

        // Проверка столкновения с другими элементами
        checkCollision(ball);
      }
      requestAnimationFrame(move);
    }

    move();
  }

  // Проверка столкновения с другими элементами
  function checkCollision(ball) {
    const rect1 = ball.element.getBoundingClientRect();
    const elements = document.querySelectorAll('.element');

    elements.forEach((element) => {
      const rect2 = element.getBoundingClientRect();
      if (
        rect1.right > rect2.left &&
        rect1.left < rect2.right &&
        rect1.bottom > rect2.top &&
        rect1.top < rect2.bottom
      ) {
        handleCollision(ball, element);
      }
    });
  }

  // Обработка столкновения
  function handleCollision(ball, element) {
    const ballRect = ball.element.getBoundingClientRect();

    const ballPrevX = ballRect.left + gameConfig.ballsSize / 2 - ball.velX * bonusSpeed.current;
    const ballPrevY = ballRect.top + gameConfig.ballsSize / 2 - ball.velY * bonusSpeed.current;

    if (element.classList.contains('triangle')) {
      const vertices = getTriangleVertices(
        element,
        element.getBoundingClientRect()
      );
      if (
        isPointInTriangle({ x: ballRect.left, y: ballRect.top }, vertices) ||
        isPointInTriangle({ x: ballRect.right, y: ballRect.top }, vertices) ||
        isPointInTriangle({ x: ballRect.left, y: ballRect.bottom }, vertices) ||
        isPointInTriangle({ x: ballRect.right, y: ballRect.bottom }, vertices)
      ) {
        const side = getCollisionSideTriangle(
          ballPrevX,
          ballPrevY,
          ballRect.left + gameConfig.ballsSize / 2,
          ballRect.top + gameConfig.ballsSize / 2,
          element
        );
        if (side === 'hypotenuse') {
          // Логика для гипотенузы
          // Проверка остроты угла
          const triangleType = element.dataset.mainAngle;
          if (triangleType === 'LT' || triangleType === 'RB') {
            // Грани типа `/`
            if (Math.sign(ball.velX) !== Math.sign(ball.velY)) {
              // Одинаковые знаки - тупой угол
              [ball.velX, ball.velY] = [-ball.velY, -ball.velX];
            } else {
              // Разные знаки - острый угол
              [ball.velX, ball.velY] = [-ball.velX, -ball.velY];
            }
          } else if (triangleType === 'LB' || triangleType === 'RT') {
            // Грани типа `\`
            if (Math.sign(ball.velX) === Math.sign(ball.velY)) {
              // Разные знаки - тупой угол
              [ball.velX, ball.velY] = [ball.velY, ball.velX];
            } else {
              // Одинаковые знаки - острый угол
              [ball.velX, ball.velY] = [-ball.velX, -ball.velY];
            }
          }
        } else {
          // Логика для сторон треугольника
          if (side === 'left' || side === 'right') {
            ball.velX = -ball.velX;
          } else if (side === 'top' || side === 'bottom') {
            ball.velY = -ball.velY;
          }
        }
        // Уменьшение счетчика и удаление элемента, если он достиг 0
        const counter = element.querySelector('.counter_container span');
        if (counter) {
          let stacks = parseInt(counter.innerText);
          stacks--;
          elementsListState.rows.forEach((row) =>
            row.forEach((currentElement) =>
              currentElement && currentElement.id === element.id
                ? (currentElement.stacks -= 1)
                : null
            )
          );
          if (stacks <= 0) {
            element.style.display = 'none';
            setExpValue((prevState) => prevState + parseInt(element.dataset.exp));
            elementsListState.rows.forEach((row) =>
              row.forEach((currentElement, index) =>
                currentElement && currentElement.id === element.id
                  ? (row[index] = undefined)
                  : null
              )
            );
          } else {
            counter.innerText = stacks;
          }
        }
      }
    } else if (element.classList.contains('box')) {
      const side = getCollisionSideBox(
        ballPrevX,
        ballPrevY,
        ballRect.left + gameConfig.ballsSize / 2,
        ballRect.top + gameConfig.ballsSize / 2,
        element
      );
      if (side === 'left' || side === 'right') {
        ball.velX = -ball.velX;
      } else if (side === 'top' || side === 'bottom') {
        ball.velY = -ball.velY;
      }
      // Уменьшение счетчика и удаление элемента, если он достиг 0
      const counter = element.querySelector('.counter_container span');
      if (counter) {
        let stacks = parseInt(counter.innerText);
        stacks--;
        elementsListState.rows.forEach((row) =>
          row.forEach((currentElement) =>
            currentElement && currentElement.id === element.id
              ? (currentElement.stacks -= 1)
              : null
          )
        );
        if (stacks <= 0) {
          element.style.display = 'none';
          setExpValue((prevState) => prevState + parseInt(element.dataset.exp));
          elementsListState.rows.forEach((row) =>
            row.forEach((currentElement, index) =>
              currentElement && currentElement.id === element.id
                ? (row[index] = undefined)
                : null
            )
          );
        } else {
          counter.innerText = stacks;
        }
      }
    } else if (element.classList.contains('add-balls-bonus')) {
      const circleElement = element.querySelector('.circle-bonus');
      const circleElementRect = circleElement.getBoundingClientRect();

      if (
        isPointInCircle(ballRect.left, ballRect.top, {
          x: circleElementRect.left + circleElementRect.width / 2,
          y: circleElementRect.top + circleElementRect.height / 2,
          r: circleElementRect.width / 2,
        }) ||
        isPointInCircle(ballRect.left, ballRect.bottom, {
          x: circleElementRect.left + circleElementRect.width / 2,
          y: circleElementRect.top + circleElementRect.height / 2,
          r: circleElementRect.width / 2,
        }) ||
        isPointInCircle(ballRect.right, ballRect.top, {
          x: circleElementRect.left + circleElementRect.width / 2,
          y: circleElementRect.top + circleElementRect.height / 2,
          r: circleElementRect.width / 2,
        }) ||
        isPointInCircle(ballRect.right, ballRect.bottom, {
          x: circleElementRect.left + circleElementRect.width / 2,
          y: circleElementRect.top + circleElementRect.height / 2,
          r: circleElementRect.width / 2,
        })
      ) {
        const extraBalls = element.dataset.extraBalls;
        extraBallsQue += +extraBalls;
        element.style.display = 'none';
        elementsListState.rows.forEach((row) =>
          row.forEach((currentElement, index) =>
            currentElement && currentElement.id === element.id
              ? (row[index] = undefined)
              : null
          )
        );
        updateStoppedCounter();
        updateWaitingCounter();
      }
    } else if (element.classList.contains('laser-bonus')) {
      const circleElement = element.querySelector('.circle-bonus');
      const circleElementRect = circleElement.getBoundingClientRect();
      const containerRect = container.current.getBoundingClientRect();

      if (
        isPointInCircle(ballRect.left, ballRect.top, {
          x: circleElementRect.left + circleElementRect.width / 2,
          y: circleElementRect.top + circleElementRect.height / 2,
          r: circleElementRect.width / 2,
        }) ||
        isPointInCircle(ballRect.left, ballRect.bottom, {
          x: circleElementRect.left + circleElementRect.width / 2,
          y: circleElementRect.top + circleElementRect.height / 2,
          r: circleElementRect.width / 2,
        }) ||
        isPointInCircle(ballRect.right, ballRect.top, {
          x: circleElementRect.left + circleElementRect.width / 2,
          y: circleElementRect.top + circleElementRect.height / 2,
          r: circleElementRect.width / 2,
        }) ||
        isPointInCircle(ballRect.right, ballRect.bottom, {
          x: circleElementRect.left + circleElementRect.width / 2,
          y: circleElementRect.top + circleElementRect.height / 2,
          r: circleElementRect.width / 2,
        })
      ) {
        if (ball.element.dataset.isThisBonusUsed === 'false') {
          const laserRect = element.getBoundingClientRect();
          ball.element.dataset.isThisBonusUsed = 'true';
          element.dataset.activated = 'true';

          // Создание контейнера лазера
          if (element.dataset.direction === 'TB') {
            createLaserContainer(
              laserRect.left -
                containerRect.left +
                gameConfig.elementsSize / 2 -
                (laserRect.width * 0.3) / 2,
              0,
              laserRect.width * 0.3,
              containerRect.height
            );
          } else if (element.dataset.direction === 'LR') {
            createLaserContainer(
              0,
              laserRect.top -
                containerRect.top +
                gameConfig.elementsSize / 2 -
                (laserRect.height * 0.3) / 2,
              containerRect.width,
              laserRect.height * 0.3
            );
          }
          elementsListState.rows.forEach((row) =>
            row.forEach((currentElement, index) =>
              currentElement && currentElement.id === element.id
                ? (row[index] = undefined)
                : null
            )
          );
        }
      } else {
        ball.element.dataset.isThisBonusUsed = 'false';
      }
    } else if (element.classList.contains('fire-bonus')) {
      const side = getCollisionSideBox(
        ballPrevX,
        ballPrevY,
        ballRect.left + gameConfig.ballsSize / 2,
        ballRect.top + gameConfig.ballsSize / 2,
        element
      );
      if (side === 'left' || side === 'right') {
        ball.velX = -ball.velX;
      } else if (side === 'top' || side === 'bottom') {
        ball.velY = -ball.velY;
      }
      // Уменьшение счетчика и удаление элемента, если он достиг 0
      const counter = element.querySelector('.counter_container span');
      if (counter) {
        let stacks = parseInt(counter.innerText);
        stacks--;
        elementsListState.rows.forEach((row) =>
          row.forEach((currentElement) =>
            currentElement && currentElement.id === element.id
              ? (currentElement.stacks -= 1)
              : null
          )
        );
        if (stacks <= 0) {
          const laserRect = element.getBoundingClientRect();
          ball.element.dataset.isThisBonusUsed = 'true';
          element.dataset.activated = 'true';

          const containerRect = container.current.getBoundingClientRect();

          // Создание контейнера лазера
          if (element.dataset.direction === 'TB') {
            createLaserContainer(
              laserRect.left -
                containerRect.left +
                gameConfig.elementsSize / 2 -
                (laserRect.width * 0.3) / 2,
              0,
              laserRect.width * 0.3,
              containerRect.height,
              true
            );
          } else if (element.dataset.direction === 'LR') {
            createLaserContainer(
              0,
              laserRect.top -
                containerRect.top +
                gameConfig.elementsSize / 2 -
                (laserRect.height * 0.3) / 2,
              containerRect.width,
              laserRect.height * 0.3,
              true
            );
          }
          element.style.display = 'none';
          setExpValue((prevState) => prevState + parseInt(element.dataset.exp));
          elementsListState.rows.forEach((row) =>
            row.forEach((currentElement, index) =>
              currentElement && currentElement.id === element.id
                ? (row[index] = undefined)
                : null
            )
          );
        } else {
          counter.innerText = stacks;
        }
      }
    }
  }

  function getTriangleVertices(triangle, rect) {
    let vertices;

    const triangleType = triangle.dataset.mainAngle;

    switch (triangleType) {
      case 'RT':
        vertices = [
          { x: rect.left, y: rect.top },
          { x: rect.right, y: rect.top },
          { x: rect.right, y: rect.bottom },
        ];
        break;
      case 'LB':
        vertices = [
          { x: rect.left, y: rect.top },
          { x: rect.left, y: rect.bottom },
          { x: rect.right, y: rect.bottom },
        ];
        break;
      case 'RB':
        vertices = [
          { x: rect.right, y: rect.top },
          { x: rect.right, y: rect.bottom },
          { x: rect.left, y: rect.bottom },
        ];
        break;
      case 'LT':
        vertices = [
          { x: rect.left, y: rect.bottom },
          { x: rect.left, y: rect.top },
          { x: rect.right, y: rect.top },
        ];
        break;
      default:
        break;
    }

    return vertices;
  }

  // Функция для проверки, находится ли точка внутри треугольника
  function isPointInTriangle(pt, v) {
    const areaOrig = Math.abs(
      (v[0].x * (v[1].y - v[2].y) +
        v[1].x * (v[2].y - v[0].y) +
        v[2].x * (v[0].y - v[1].y)) /
        2.0
    );
    const area1 = Math.abs(
      (pt.x * (v[1].y - v[2].y) +
        v[1].x * (v[2].y - pt.y) +
        v[2].x * (pt.y - v[1].y)) /
        2.0
    );
    const area2 = Math.abs(
      (v[0].x * (pt.y - v[2].y) +
        pt.x * (v[2].y - v[0].y) +
        v[2].x * (v[0].y - pt.y)) /
        2.0
    );
    const area3 = Math.abs(
      (v[0].x * (v[1].y - pt.y) +
        v[1].x * (pt.y - v[0].y) +
        pt.x * (v[0].y - v[1].y)) /
        2.0
    );
    return area1 + area2 + area3 === areaOrig;
  }

  // Функция для проверки, находится ли точка внутри круга
  function isPointInCircle(x, y, circle) {
    const dx = x - circle.x;
    const dy = y - circle.y;
    const distanceSquared = dx * dx + dy * dy;
    const radiusSquared = circle.r * circle.r;

    return distanceSquared <= radiusSquared;
  }

  // Определение стороны столкновения c box
  function getCollisionSideBox(prevX, prevY, x, y, element) {
    const rect = element.getBoundingClientRect();

    const left = {
      x1: rect.left,
      y1: rect.top,
      x2: rect.left,
      y2: rect.bottom,
    };
    const right = {
      x1: rect.right,
      y1: rect.top,
      x2: rect.right,
      y2: rect.bottom,
    };
    const top = { x1: rect.left, y1: rect.top, x2: rect.right, y2: rect.top };
    const bottom = {
      x1: rect.left,
      y1: rect.bottom,
      x2: rect.right,
      y2: rect.bottom,
    };

    const distanceToLeft = intersectsRay(
      prevX + gameConfig.ballsSize / 2,
      prevY,
      x + gameConfig.ballsSize / 2,
      y,
      left
    );
    const distanceToRight = intersectsRay(
      prevX - gameConfig.ballsSize / 2,
      prevY,
      x - gameConfig.ballsSize / 2,
      y,
      right
    );
    const distanceToTop = intersectsRay(
      prevX,
      prevY + gameConfig.ballsSize / 2,
      x,
      y + gameConfig.ballsSize / 2,
      top
    );
    const distanceToBottom = intersectsRay(
      prevX,
      prevY - gameConfig.ballsSize / 2,
      x,
      y - gameConfig.ballsSize / 2,
      bottom
    );

    const minDistance = Math.min(
      distanceToLeft,
      distanceToRight,
      distanceToTop,
      distanceToBottom
    );

    switch (minDistance) {
      case distanceToLeft:
        return 'left';

      case distanceToRight:
        return 'right';

      case distanceToTop:
        return 'top';

      case distanceToBottom:
        return 'bottom';

      default:
        return null;
    }
  }

  // Определение стороны столкновения c triangle
  function getCollisionSideTriangle(prevX, prevY, x, y, element) {
    const rect = element.getBoundingClientRect();
    const mainAngle = element.getAttribute('data-main-angle');

    const left = {
      x1: rect.left,
      y1: rect.top,
      x2: rect.left,
      y2: rect.bottom,
    };
    const right = {
      x1: rect.right,
      y1: rect.top,
      x2: rect.right,
      y2: rect.bottom,
    };
    const top = { x1: rect.left, y1: rect.top, x2: rect.right, y2: rect.top };
    const bottom = {
      x1: rect.left,
      y1: rect.bottom,
      x2: rect.right,
      y2: rect.bottom,
    };

    let minDistance;

    const distanceToLeft = intersectsRay(
      prevX + gameConfig.ballsSize / 2,
      prevY,
      x + gameConfig.ballsSize / 2,
      y,
      left
    );
    const distanceToRight = intersectsRay(
      prevX - gameConfig.ballsSize / 2,
      prevY,
      x - gameConfig.ballsSize / 2,
      y,
      right
    );
    const distanceToTop = intersectsRay(
      prevX,
      prevY + gameConfig.ballsSize / 2,
      x,
      y + gameConfig.ballsSize / 2,
      top
    );
    const distanceToBottom = intersectsRay(
      prevX,
      prevY - gameConfig.ballsSize / 2,
      x,
      y - gameConfig.ballsSize / 2,
      bottom
    );
    let distanceToHypotenuse;
    // Проверка для гипотенузы треугольника
    let hypotenuse;
    if (mainAngle === 'RB') {
      hypotenuse = {
        x1: rect.left,
        y1: rect.bottom,
        x2: rect.right,
        y2: rect.top,
      };
      distanceToHypotenuse = intersectsRay(
        prevX + gameConfig.ballsSize / 2,
        prevY + gameConfig.ballsSize / 2,
        x + gameConfig.ballsSize / 2,
        y + gameConfig.ballsSize / 2,
        hypotenuse
      );
      minDistance = Math.min(
        distanceToRight,
        distanceToBottom,
        distanceToHypotenuse
      );
    } else if (mainAngle === 'LB') {
      hypotenuse = {
        x1: rect.right,
        y1: rect.bottom,
        x2: rect.left,
        y2: rect.top,
      };
      distanceToHypotenuse = intersectsRay(
        prevX - gameConfig.ballsSize / 2,
        prevY + gameConfig.ballsSize / 2,
        x - gameConfig.ballsSize / 2,
        y + gameConfig.ballsSize / 2,
        hypotenuse
      );
      minDistance = Math.min(
        distanceToLeft,
        distanceToBottom,
        distanceToHypotenuse
      );
    } else if (mainAngle === 'RT') {
      hypotenuse = {
        x1: rect.left,
        y1: rect.top,
        x2: rect.right,
        y2: rect.bottom,
      };
      distanceToHypotenuse = intersectsRay(
        prevX + gameConfig.ballsSize / 2,
        prevY - gameConfig.ballsSize / 2,
        x + gameConfig.ballsSize / 2,
        y - gameConfig.ballsSize / 2,
        hypotenuse
      );
      minDistance = Math.min(
        distanceToRight,
        distanceToTop,
        distanceToHypotenuse
      );
    } else if (mainAngle === 'LT') {
      hypotenuse = {
        x1: rect.right,
        y1: rect.top,
        x2: rect.left,
        y2: rect.bottom,
      };
      distanceToHypotenuse = intersectsRay(
        prevX - gameConfig.ballsSize / 2,
        prevY - gameConfig.ballsSize / 2,
        x - gameConfig.ballsSize / 2,
        y - gameConfig.ballsSize / 2,
        hypotenuse
      );
      minDistance = Math.min(
        distanceToLeft,
        distanceToTop,
        distanceToHypotenuse
      );
    }

    switch (minDistance) {
      case distanceToLeft:
        return 'left';

      case distanceToRight:
        return 'right';

      case distanceToTop:
        return 'top';

      case distanceToBottom:
        return 'bottom';

      case distanceToHypotenuse:
        return 'hypotenuse';

      default:
        return null;
    }
  }

  // Проверка пересечения линий
  function intersectsRay(x1, y1, x2, y2, line) {
    const dx1 = x2 - x1;
    const dy1 = y2 - y1;
    const dx2 = line.x2 - line.x1;
    const dy2 = line.y2 - line.y1;

    const det = dx1 * dy2 - dx2 * dy1;

    if (det === 0) return 10000; // Линии параллельны

    const t1 = -((line.y1 - y1) * dx2 - (line.x1 - x1) * dy2) / det;

    if (t1 < 0) return 10000; // Луч пересекается с линией в обратном направлении

    const intersectionX = x1 + t1 * dx1;
    const intersectionY = y1 + t1 * dy1;
    const distance = Math.sqrt(
      Math.pow(intersectionX - x2, 2) + Math.pow(intersectionY - y2, 2)
    );

    return distance;
  }

  function createLaserContainer(x, y, width, height, isFire) {
    const laserContainer = document.createElement('div');
    laserContainer.className = 'laser-container';
    laserContainer.style.backgroundColor = isFire ? '#e78f00' : null;
    laserContainer.style.left = x + 'px';
    laserContainer.style.top = y + 'px';
    laserContainer.style.width = width + 'px';
    laserContainer.style.height = height + 'px';
    container.current.appendChild(laserContainer);

    checkIntersections(laserContainer.getBoundingClientRect(), isFire);

    setTimeout(() => {
      laserContainer.remove();
    }, 100);
  }

  // Проверка пересечения лазера с элементами
  function checkIntersections(laserRect, isFire) {
    const elements = document.querySelectorAll(
      '.box:not(.metal_box), .triangle, .fire-bonus'
    );
    elements.forEach((element) => {
      const elementRect = element.getBoundingClientRect();
      if (
        laserRect.left < elementRect.right &&
        laserRect.right > elementRect.left &&
        laserRect.top < elementRect.bottom &&
        laserRect.bottom > elementRect.top
      ) {
        decreaseStacks(element, isFire);
      }
    });
  }

  // Снятие стаков с элемента
  function decreaseStacks(element, isFire) {
    let stacks = parseInt(element.getAttribute('data-stacks'), 10);
    if (stacks <= 0 || isFire) {
      element.style.display = 'none';
      setExpValue((prevState) => prevState + parseInt(element.dataset.exp));
      elementsListState.rows.forEach((row) =>
        row.forEach((currentElement, index) =>
          currentElement && currentElement.id === element.id
            ? (row[index] = undefined)
            : null
        )
      );
    } else {
      element.setAttribute('data-stacks', stacks - 1);
      element.querySelector('span').innerText = stacks - 1;
    }
  }

  function updateWaitingCounter() {
    if (!pageWasClosed.current) {
      const counterValue =
        waitingContainer.current.getElementsByClassName('ball').length;
      waitingCounter.current.innerText =
        counterValue === 0 ? '' : `x${counterValue}`;
    }
  }

  function updateStoppedCounter() {
    if (!pageWasClosed.current) {
      const counterValue =
        stoppedContainer.current.getElementsByClassName('ball').length;
      stoppedCounter.current.innerText =
        counterValue === 0 ? '' : `x${counterValue}`;
    }
  }

  function stopBallAnimation(ball) {
    if (!ball.element.classList.contains('moving')) {
      ball.element.classList.add('moving');
      ball.element.style.left =
        stopPosition.left - gameConfig.ballsSize / 2 + 'px';
      ball.element.style.top = stopPosition.top + 'px';
      setTimeout(() => {
        if (!pageWasClosed.current) {
          stoppedContainer.current.appendChild(ball.element);
          ball.element.classList.remove('moving');
          ball.element.style.transform = '';
          ball.element.style.left = 0 - gameConfig.ballsSize / 2 + 'px';
          ball.element.style.top = 0;
          balls = balls.filter(
            (filteredBall) => filteredBall.element !== ball.element
          );
        }
      }, 100);
    }
  }

  function checkAllBallsStopped() {
    if (
      balls.length === 0 &&
      waitingBalls.every(
        (ball) => ball.element.parentNode === stoppedContainer.current
      )
    ) {
      waitingBalls.forEach((ball) => {
        waitingContainer.current.style.left =
          stopPosition.left + gameConfig.ballsSize / 2 + 'px';
        waitingContainer.current.style.top =
          container.clientHeight - gameConfig.ballsSize + 'px';
        waitingContainer.current.appendChild(ball.element);
      });
      updateStoppedCounter();
      updateWaitingCounter();
      stopPosition.isUpdated = false;
      isAnimating = false;
      forceStopBallsMoving.current = false;
      setElementsStacksState(
        (prevElementsStacksState) => prevElementsStacksState + 1
      );
      setNoActiveGameZonePanelVisible('flex');
      setStopButtonVisible('none');
      setScrollPanelVisible('none');
      setBonusesPanelVisible('flex');
      setAnimationRunning(false);
      clearMetalBoxes();
      const filteredBalls = waitingBalls.filter((ball) => !ball.forOneUse);
      setWaitingBalls([...filteredBalls]);
      setWaitingCounterState(filteredBalls.length + extraBallsQue);
      if (extraBallsQue !== 0) {
        createBalls(extraBallsQue);
        extraBallsQue = 0;
      }
      setGameOver(true);
    }
  }

  function startBalls(velX, velY) {
    if (isAnimating) return;
    isAnimating = true;
    let delay = 0;
    const startedPosition = {
      left: stopPosition.left,
      top: stopPosition.top,
    };
    setTimeout(() => {
      bonusSpeed.current += 0.5;
    }, 5000);
    setTimeout(() => {
      bonusSpeed.current += 0.5;
    }, 10000);
    setStopButtonVisible('block');
    setNoActiveGameZonePanelVisible('none');
    waitingBalls.forEach((ball, index) => {
      ball.element = document.getElementById(ball.elementKey);
      setTimeout(() => {
        if (pageWasClosed.current) return;
        ball.velX = velX;
        ball.velY = velY;
        container.current.appendChild(ball.element);
        ball.element.style.left = stopPosition.left + 'px';
        ball.element.style.top = stopPosition.top + 'px';
        balls.push(ball);
        moveBall(ball, startedPosition);
        updateWaitingCounter();
      }, delay);
      delay += 100;
    });
  }

  // Отображение траектории и добавление слушателей событий
  let trajectoryElement;

  function mouseUpHandler(event) {
    if (isGameOver || isNaN(stopPosition.left)) return;
    // Позиция мыши в момент отпускания кнопки
    const releaseX =
      event.clientX - container.current.getBoundingClientRect().left;
    const releaseY =
      event.clientY - container.current.getBoundingClientRect().top;

    // Вычисление направления движения
    const deltaX = releaseX - stopPosition.left - gameConfig.ballsSize / 2;
    const deltaY = releaseY - stopPosition.top;
    const distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY);

    // Задание скорости и направления
    let velX = (deltaX / distance) * BALL_SPEED; // Коэффициент скорости
    let velY = (deltaY / distance) * BALL_SPEED;

    // Запуск анимации
    startBalls(velX, velY);
    setAnimationRunning(true);

    container.current.removeEventListener('mousedown', startDrawingTrajectory);
    container.current.removeEventListener('mousemove', updateTrajectory);

    // Удаление траектории
    clearTrajectory();
  }

  function startDrawingTrajectory(event) {
    if (isAnimating || isGameOver) return;
    clearTrajectory();
    const { clientX, clientY } = event;
    const startX = stopPosition.left + gameConfig.ballsSize / 2;
    const startY = stopPosition.top + gameConfig.ballsSize / 2;
    const endX =
      clientX -
      container.current.getBoundingClientRect().left +
      gameConfig.ballsSize / 2;
    const endY =
      clientY -
      container.current.getBoundingClientRect().top +
      gameConfig.ballsSize / 2;
    drawTrajectory(startX, startY, endX, endY);
  }

  function updateTrajectory(event) {
    if (!trajectoryElement || isNaN(stopPosition.left) || isAnimating) return;
    const { clientX, clientY } = event;
    const startX = stopPosition.left + gameConfig.ballsSize / 2;
    const startY = stopPosition.top + gameConfig.ballsSize / 2;
    const endX =
      clientX -
      container.current.getBoundingClientRect().left +
      gameConfig.ballsSize / 2;
    const endY =
      clientY -
      container.current.getBoundingClientRect().top +
      gameConfig.ballsSize / 2;
    drawTrajectory(startX, startY, endX, endY);
  }

  // Функция для обновления направления мяча на основе положения скроллера
  function updateScrollTrajectory(event) {
    const angle = +event.target.value;
    const radian = (angle - 90) * (Math.PI / 180); // Преобразуем в радианы

    const velX = Math.cos(radian - Math.PI / 2) * gameConfig.ballSpeed;
    const velY = Math.sin(radian - Math.PI / 2) * gameConfig.ballSpeed;

    // Теперь вызываем функцию для отрисовки траектории
    const startX = stopPosition.left + gameConfig.ballsSize / 2;
    const startY = stopPosition.top + gameConfig.ballsSize / 2;

    // Учитываем, что при угле 0 мяч должен лететь влево, а при 180 - вправо
    const endX = startX + velX * 100; // Увеличиваем длину траектории для большей визуализации
    const endY = startY + velY * 100;

    drawTrajectory(startX, startY, endX, endY);
  }

  // Функция запуска мяча с использованием скроллера
  function launchBallFromScroll(event) {
    clearTrajectory();
    const angle = +event.target.value;
    const radian = (angle - 90) * (Math.PI / 180);

    const velX = Math.cos(radian - Math.PI / 2) * gameConfig.ballSpeed;
    const velY = Math.sin(radian - Math.PI / 2) * gameConfig.ballSpeed;

    startBalls(velX, velY); // Запускаем мяч с рассчитанными скоростями
    event.target.value = 90;
  }

  function drawTrajectory(startX, startY, endX, endY) {
    if (isNaN(stopPosition.left)) return;
    clearTrajectory();

    const deltaX = endX - startX;
    const deltaY = endY - startY;
    const distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY);

    let velX = (deltaX / distance) * 8;
    let velY = (deltaY / distance) * 8;

    trajectoryElement = document.createElement('div');
    trajectoryElement.classList.add('trajectory');

    let currentX = startX;
    let currentY = startY;
    let currentDistance = 0;

    while (currentDistance < container.current.offsetHeight * 1.2) {
      currentX += velX;
      currentY += velY;
      currentDistance += Math.sqrt(
        (currentX - startX) ** 2 + (currentY - startY) ** 2
      );

      if (currentX <= 0 || currentX >= container.current.clientWidth) {
        velX *= -1;
        currentX = Math.min(
          Math.max(currentX, 0),
          container.current.clientWidth
        );
      }

      if (currentY <= 0 || currentY >= container.current.clientHeight) {
        velY *= -1;
        currentY = Math.min(
          Math.max(currentY, 0),
          container.current.clientHeight
        );
      }

      const segment = createTrajectorySegment(
        startX,
        startY,
        currentX,
        currentY
      );
      trajectoryElement.appendChild(segment);
      startX = currentX;
      startY = currentY;
    }

    container.current.appendChild(trajectoryElement);
  }

  function createTrajectorySegment(x1, y1, x2, y2) {
    const segment = document.createElement('div');
    segment.classList.add('trajectory-segment');

    const length = Math.sqrt((x2 - x1) ** 2 + (y2 - y1) ** 2);
    const angle = (Math.atan2(y2 - y1, x2 - x1) * 180) / Math.PI;

    segment.style.width = `${length / 2}px`;
    segment.style.left = 0 - length / 2 - gameConfig.ballsSize/2 + 'px';
    segment.style.transform = `translate(${x1}px, ${y1}px) rotate(${angle}deg)`;

    return segment;
  }

  function clearTrajectory() {
    if (trajectoryElement) {
      trajectoryElement.remove();
      trajectoryElement = null;
    }
  }

  const [elementsListState, setElementsListState] = useState({
    rows: [new Array(ELEMENTS_BY_ROW).fill(undefined)],
  });
  // Создание новой строки блоков со сдвигом вниз
  function addNewLineClassicMode(size) {
    elementsListState.rows.forEach((row) =>
      row.forEach((element) => (element ? (element.y += element.size) : null))
    );

    const newLine = generateLine(newLineGenerating(size));

    setElementsListState({
      rows: [...elementsListState.rows, ...newLine.rows],
    });
  }

  // Генерация новой линии со случайными блоками (предпоследний блок - всегда бонусный мяч)
  function generateLine(config) {
    const rewritedElements = config.rows.map((row, rowIndex) => {
      return row.map((cell, cellIndex) => {
        if (cell) {
          const x = cellIndex * gameConfig.elementsSize;
          const y = rowIndex * gameConfig.elementsSize;

          cell.id = `element-${rowIndex}-${cellIndex}-${Date.now()}`;
          cell.x = x;
          cell.y = y;

          return cell;
        }
        return undefined;
      });
    });
    return { ...config, rows: rewritedElements };
  }

  // Генерация строки случайных элементов (!дополнить типами элементов)
  function newLineGenerating(length) {
    function createRandomElement(stack) {
      const randomNumber = Math.floor(Math.random() * 100);
      const isDoubleStack =
        Math.floor(Math.random() * 100) === 1 ? true : false;
      if (isDoubleStack) {
        stack *= 2;
      }
      if (randomNumber >= 0 && randomNumber < 15) {
        return {
          type: 'box',
          stacks: stack,
          exp: stack,
          size: gameConfig.elementsSize,
        };
      } else if (randomNumber >= 15 && randomNumber < 50) {
        const randomTriangleAngleType = Math.floor(Math.random() * 4);
        switch (randomTriangleAngleType) {
          case 0:
            return {
              type: 'triangle',
              mainAngle: 'RB',
              stacks: stack,
              exp: stack,
              size: gameConfig.elementsSize,
            };
          case 1:
            return {
              type: 'triangle',
              mainAngle: 'LB',
              stacks: stack,
              exp: stack,
              size: gameConfig.elementsSize,
            };
          case 2:
            return {
              type: 'triangle',
              mainAngle: 'RT',
              stacks: stack,
              exp: stack,
              size: gameConfig.elementsSize,
            };
          case 3:
            return {
              type: 'triangle',
              mainAngle: 'LT',
              stacks: stack,
              exp: stack,
              size: gameConfig.elementsSize,
            };
          default:
            return undefined;
        }
      } else if (randomNumber >= 50 && randomNumber < 98) {
        return;
      } else if (randomNumber >= 98 && randomNumber < 100) {
        const randomType = Math.floor(Math.random() * 2);
        switch (randomType) {
          case 0:
            return {
              type: 'fire',
              direction: 'TB',
              stacks: stack,
              exp: stack,
              size: gameConfig.elementsSize,
            };
          case 1:
            return {
              type: 'fire',
              direction: 'LR',
              stacks: stack,
              exp: stack,
              size: gameConfig.elementsSize,
            };
          default:
            return undefined;
        }
      } else {
        return;
      }
    }
    const newLine = {
      rows: [[], [], [], [], [], [], [], [], [], []],
    };
    for (let i = 0; i < length; i++) {
      newLine.rows[0].push(undefined);
    }
    for (let index = 1; index < 9; index++) {
      for (let i = 0; i < length; i++) {
        newLine.rows[index].push(createRandomElement(20 + Math.floor(Math.random()*50)));
      }
    }
    return newLine;
  }

  // Раскол, функция, снижающая прочность всех блоков на 33%
  function crackBlocks() {
    const elementsListClone = [...elementsListState.rows];
    elementsListClone.forEach((row) =>
      row.forEach((element, index) => {
        if (
          element &&
          (element.type === 'box' || element.type === 'triangle')
        ) {
          const slicedStacks = Math.floor(element.stacks * 0.66);
          element.stacks = slicedStacks;
          if (slicedStacks <= 0) {
            const currentElement = document.getElementById(element.id);
            currentElement.style.display = 'none';
            row[index] = undefined;
          }
        }
      })
    );
    setElementsListState({ rows: [...elementsListClone] });
  }

  // Удвоение количества шаров на 1 ход
  function extraBallsForRaund() {
    setWaitingCounterState(waitingBalls.length * 2);
    createBalls(waitingBalls.length, true);
  }

  // Удаление нижней линии
  function destroyFirstLine() {
    const elementsListClone = [...elementsListState.rows];
    const lineForDeletingNumber = elementsListState.rows.findIndex(
      (row) =>
        !row.every(
          (element) =>
            element === undefined ||
            element.type === 'addBalls' ||
            element.type === 'laser'
        )
    );
    elementsListClone[lineForDeletingNumber].forEach((element, index) => {
      if (element && (element.type === 'box' || element.type === 'triangle')) {
        const currentElement = document.getElementById(element.id);
        currentElement.style.display = 'none';
        elementsListClone[lineForDeletingNumber][index] = undefined;
      }
    });
    setElementsListState({ rows: [...elementsListClone] });
  }

  // добавить 4 случайных лазера
  function addRandomLasers() {
    const undefinedCoordinates = [];

    // Проходим по всем элементам в rows
    elementsListState.rows.forEach((row, rowIndex) => {
      row.forEach((element, colIndex) => {
        if (element === undefined) {
          // Добавляем координаты undefined в массив
          undefinedCoordinates.push([rowIndex, colIndex]);
        }
      });
    });

    const lasersCounter =
      undefinedCoordinates.length < 4 ? undefinedCoordinates.length : 4;
    const selectedCoordinates = [];

    // Выбираем случайные, не повторяющиеся координаты
    while (selectedCoordinates.length < lasersCounter) {
      const randomIndex = Math.floor(
        Math.random() * undefinedCoordinates.length
      );
      const [row, col] = undefinedCoordinates[randomIndex];

      // Удаляем выбранный элемент из списка, чтобы избежать дубликатов
      undefinedCoordinates.splice(randomIndex, 1);

      selectedCoordinates.push([row, col]);
      const x = col * gameConfig.elementsSize;
      const y =
        (elementsListState.rows.length - row - 1) * gameConfig.elementsSize;

      const randomDirection = Math.floor(Math.random() * 2) === 0 ? 'TB' : 'LR';

      elementsListState.rows[row][col] = {
        id: `element-${row}-${col}-${Date.now()}`,
        x: x,
        y: y,
        type: 'laser',
        direction: randomDirection,
        size: gameConfig.elementsSize,
      };
    }
    setElementsListState({ ...elementsListState });
  }

  const [isMetalBoxesShowed, setMetalBoxesShowed] = useState(false);
  // добавить 5 блоков для защиты от падения
  function addMetalBoxes() {
    setMetalBoxesShowed(true);
  }

  // Очистить металические блоки
  function clearMetalBoxes() {
    setMetalBoxesShowed(false);
  }

  const metalBoxesList = [{}, {}, {}, {}, {}].map((el, index) => {
    return {
      size: gameConfig.elementsSize,
      x: gameConfig.elementsSize * index,
    };
  });
  
  return (
    <>
      <div className='classic_mode' id='classic_mode'>
        <button onClick={goHomeHandler}>Home</button>
        <button onClick={pauseGame}>Pause</button>
        <div className='exp_container'>
          <span className='exp_value'>{expValue}</span>
        </div>
        <div
          ref={container}
          id='container'
          onMouseDown={!animationRunning ? startDrawingTrajectory : null}
          onMouseMove={!animationRunning ? updateTrajectory : null}
          onMouseUp={!animationRunning ? mouseUpHandler : null}
        >
          {elementsListState.rows.length > 0 ? (
            <Elements rows={elementsListState.rows} />
          ) : null}
          {isMetalBoxesShowed
            ? metalBoxesList.map((el, index) => (
                <MetalBox
                  key={index}
                  size={el.size}
                  x={
                    stopPosition.left > container.current.offsetWidth / 2
                      ? el.x
                      : el.x + el.size * (ELEMENTS_BY_ROW - 1 - index*2)
                  }
                  y={0}
                />
              ))
            : null}
          <div
            ref={waitingContainer}
            style={waitingContainerStyle}
            id='waiting-container'
          >
            <span id='waiting-counter' ref={waitingCounter}>
              {waitingCounterState > 0 ? `x${waitingCounterState}` : null}
            </span>
            {gameConfig.updated
              ? waitingBalls.map((ball) => (
                  <Ball
                    key={ball.elementKey}
                    style={ball.style}
                    id={ball.elementKey}
                  />
                ))
              : null}
          </div>
          <div
            ref={stoppedContainer}
            style={stoppedContainerStyle}
            id='stopped-container'
          >
            <span id='stopped-counter' ref={stoppedCounter}>
              {stoppedCounterState > 0 ? `x${stoppedCounterState}` : null}
            </span>
          </div>
        </div>
        <div className='bottom_game_zone_panel'>
          <button
            ref={stopButton}
            style={{ display: stopButtonVisible }}
            onClick={stopAnimation}
            id='stop-button'
          >
            Stop Animation
          </button>
          <div
            ref={noActiveGameZonePanel}
            style={{ display: noActiveGameZonePanelVisible }}
            className='bottom_game_zone_panel-no_active'
            id='bottom_game_zone_panel-no_active'
          >
            <div
              style={{ display: bonusesPanelVisible }}
              ref={bonusPanel}
              className='bonuses_panel'
              id='bonuses_panel'
            >
              <button
                ref={scrollControlButton}
                className='scroll_control_button'
                id='scroll_control_button'
                onClick={() => {
                  setScrollPanelVisible('flex');
                  setBonusesPanelVisible('none');
                }}
              >
                SC
              </button>
              <button
                className='bonuses_panel-bonus'
                id='bonuses_panel-crack_blocks'
                onClick={crackBlocks}
              >
                <span>crack</span>
                <span
                  className='bonuses_panel-bonus-counter'
                  id='bonuses_panel-crack_blocks-counter'
                  data-stacks='99'
                >
                  x99
                </span>
              </button>
              <button
                className='bonuses_panel-bonus'
                id='bonuses_panel-extra_balls'
                onClick={extraBallsForRaund}
              >
                <span>x2</span>
                <span
                  className='bonuses_panel-bonus-counter'
                  id='bonuses_panel-extra_balls-counter'
                  data-stacks='99'
                >
                  x99
                </span>
              </button>
              <button
                className='bonuses_panel-bonus'
                id='bonuses_panel-destroy_line'
                onClick={destroyFirstLine}
              >
                <span>destroy</span>
                <span
                  className='bonuses_panel-bonus-counter'
                  id='bonuses_panel-destroy_line-counter'
                  data-stacks='99'
                >
                  x99
                </span>
              </button>
              <button
                className='bonuses_panel-bonus'
                id='bonuses_panel-add_random_lasers'
                onClick={addRandomLasers}
              >
                <span>4 lasers</span>
                <span
                  className='bonuses_panel-bonus-counter'
                  id='bonuses_panel-add_random_lasers-counter'
                  data-stacks='99'
                >
                  x99
                </span>
              </button>
              <button
                className='bonuses_panel-bonus'
                id='bonuses_panel-add_metal_boxes'
                onClick={addMetalBoxes}
              >
                <span>metal guard</span>
                <span
                  className='bonuses_panel-bonus-counter'
                  id='bonuses_panel-add_metal_boxes-counter'
                  data-stacks='99'
                >
                  x99
                </span>
              </button>
            </div>
            <div
              style={{ display: scrollPanelVisible }}
              ref={scrollPanel}
              className='scroll_panel'
              id='scroll_panel'
            >
              <button
                ref={scrollPanelArrowBack}
                className='scroll_panel-arrow_back'
                id='scroll_panel-arrow_back'
                onClick={() => {
                  setScrollPanelVisible('none');
                  setBonusesPanelVisible('flex');
                }}
              >
                {'<'}
              </button>
              <input
                type='range'
                min='5'
                max='175'
                defaultValue='90'
                id='ball_control_scroll'
                onMouseUp={launchBallFromScroll}
                onInput={updateScrollTrajectory}
              />
              <button className='scroll_panel-gold-trajectory'>aim</button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
