import AddBalls from '../elementTypes/AddBalls';
import Box from '../elementTypes/Box';
import FixedTrajectoryChange from '../elementTypes/FixedTrajectoryChange';
import Laser from '../elementTypes/Laser';
import MetalBox from '../elementTypes/MetalBox';
import MirrorTrajectoryChange from '../elementTypes/MirrorTrajectoryChange';
import RandomTrajectoryChange from '../elementTypes/RandomTrajectoryChange';
import Triangle from '../elementTypes/Triangle';

export default function ElementsLine(props) {
  const { row, line } = props;
  const createdElements = row.map((element, index) => {
    if (element) {
      switch (element.type) {
        case 'box':
          return (
            <Box
              key={index}
              x={element.x}
              y={element.y}
              line={line}
              stacks={element.stacks}
              size={element.size}
              id={element.id}
            />
          );

        case 'triangle':
          return (
            <Triangle
              key={index}
              x={element.x}
              y={element.y}
              mainAngle={element.mainAngle}
              line={line}
              stacks={element.stacks}
              size={element.size}
              id={element.id}
            />
          );

        case 'addBalls':
          return (
            <AddBalls
              key={index}
              x={element.x}
              y={element.y}
              bonus={element.bonus}
              line={line}
              size={element.size}
              id={element.id}
            />
          );

          case 'laser':
            return (
              <Laser
                key={index}
                x={element.x}
                y={element.y}
                direction={element.direction}
                line={line}
                size={element.size}
                id={element.id}
              />
            );
          case 'randomTrajectoryChange':
            return (
              <RandomTrajectoryChange
                key={index}
                x={element.x}
                y={element.y}
                line={line}
                size={element.size}
                id={element.id}
              />
            );
          case 'mirrorTrajectoryChange':
            return (
              <MirrorTrajectoryChange
                key={index}
                x={element.x}
                y={element.y}
                line={line}
                size={element.size}
                id={element.id}
              />
            );
          case 'fixedTrajectoryChange':
            return (
              <FixedTrajectoryChange
                key={index}
                x={element.x}
                y={element.y}
                direction={element.direction}
                line={line}
                size={element.size}
                id={element.id}
              />
            );
          case 'metalBox':
            return (
              <MetalBox
                key={index}
                x={element.x}
                y={element.y}
                size={element.size}
              />
            );

        default:
          return undefined;
      }
    } else return undefined
  });
  return createdElements;
}
