import { ELEMENTS_SPEED } from "../../../survivalModeConstants";
import ElementCounter from "../elementCounter/ElementCounter";

export default function Triangle(props) {
  const { x, y, mainAngle, line, stacks, size, id } = props;
  const bordersConfig = {};
  
  switch (mainAngle) {
    case 'RB':
      bordersConfig.borderLeft = `${size}px solid transparent`;
      bordersConfig.borderBottom = `${size}px solid blue`;
      break;
    case 'LB':
      bordersConfig.borderRight = `${size}px solid transparent`;
      bordersConfig.borderBottom = `${size}px solid blue`;
      break;
    case 'RT':
      bordersConfig.borderLeft = `${size}px solid transparent`;
      bordersConfig.borderTop = `${size}px solid blue`;
      break;
    case 'LT':
      bordersConfig.borderRight = `${size}px solid transparent`;
      bordersConfig.borderTop = `${size}px solid blue`;
      break;
    default:
      console.error('Main angle type error for new Triangle.');
  }

  return (
    <div
      id={id}
      className={`element triangle triangle-${mainAngle}`}
      data-stacks={stacks}
      data-main-angle={mainAngle}
      data-line={line}
      style={{
        left: `${x}px`,
        top: `${y}px`,
        width: `${0}px`,
        height: `${0}px`,
        transition: `${ELEMENTS_SPEED}s linear`,
        ...bordersConfig
      }}
    >
      <ElementCounter size={size} stacks={stacks} />
    </div>
  );
}
