import './MainPage.css';
import GameModsList from '../../popups/gameModsList/GameModsList';
import { useState } from 'react';
import { useComponentContext } from '../../contexts/tabTypeContext/TabTypeContext';
import store from './sources/store-large.png';
import collections from './sources/collections-large.png';
import mode from './sources/mode-large.png';
import tests from './sources/tests-large.png';
import mainBG from './sources/main-bg.jpg';
import arcadeGameText from './sources/arcade-game-text.png';
import arcadeGameShadow from './sources/arcade-game-shadow.png';
import mainLogo from './sources/game-logo.png';
import arcadeMachine from './sources/arcade.png';
import dailyTask from './sources/daily-task-icon.png';
import mapIcon from './sources/map.png';
import playIcon from './sources/play.png';
import calendarIcon from './sources/calendar.png';
import settingsIcon from './sources/settings.png';
import starIcon from './sources/star.png';
import gemstoneIcon from './sources/gemstone.png';
import testAvatar from '../../img/avatars/test-avatar.jpg';
import StorePopup from '../../popups/gameModsList/StorePopup';
import CollectionsPopup from '../../popups/gameModsList/CollectionsPopup';
import ModePopup from '../../popups/gameModsList/ModePopup';
import TestsPopup from '../../popups/gameModsList/TestsPopup';

export default function MainPage() {
  const { setActiveComponent } = useComponentContext();

  const goToStoryMode = () => {
    setActiveComponent('StoryModePage');
  };

  const [isStore, setStore] = useState(false);
  const [isCollections, setCollections] = useState(false);
  const [isMode, setMode] = useState(false);
  const [isTests, setTests] = useState(false);

  return (
    <>
      <div
        className='content_container'
        style={{
          backgroundImage: `url(${mainBG})`,
          backgroundSize: 'auto 100%',
          backgroundPositionX: 'center',
        }}
      >
        <div
          className='quick_play_container'
          style={{
            backgroundImage: `url(${arcadeMachine})`,
            backgroundSize: 'auto 100%',
            backgroundPositionX: 'center',
            backgroundRepeat: 'no-repeat',
          }}
        >
          <div
            className='quick_play-arcade_game'
            style={{
              backgroundImage: `url(${arcadeGameShadow})`,
              backgroundSize: 'auto 100%',
              backgroundPositionX: 'center',
              backgroundRepeat: 'no-repeat',
            }}
          >
            <img
              className='arcade_game_text'
              src={arcadeGameText}
              alt='arcade game'
            />
          </div>
          <img className='game_logo' src={mainLogo} alt='game logo' />
          <button className='quick_play_btn' onClick={goToStoryMode}>play</button>
          <div className='quick_play_btn_after'></div>
        </div>

        {/* Дальше иконки с абсолютным позиционированием */}
        <div className='profile_container'>
          <div className='profile_icon_container'>
            <div className='profile_icon_subcontainer'>
              <img src={testAvatar} alt='profile icon' />
            </div>
          </div>
          <p className='player_name absolute_text'>PLAYER07</p>
        </div>
        <div className='daily_task_container'>
          <div className='daily_task_icon_container'>
            <div className='daily_task_icon_subcontainer'>
              <img src={dailyTask} alt='daily task icon' />
            </div>
          </div>
          <div className='daily_task_timer_container'>
            <p className='daily_task_timer_subcontainer absolute_text'>2d:9h</p>
          </div>
          <p className='daily_task_title absolute_text'>daily task</p>
        </div>
        <button className='map_btn'>
          <img src={mapIcon} alt='map' />
        </button>
        <div className='settings_panel'>
          <button className='settings_panel-btn'>
            <img src={playIcon} alt='play' className='settings_panel-icon' />
          </button>
          <button className='settings_panel-btn'>
            <img src={calendarIcon} alt='calendar' className='settings_panel-icon' />
          </button>
          <button className='settings_panel-btn'>
            <img src={settingsIcon} alt='settings' className='settings_panel-icon' />
          </button>
        </div>
        <div className="cash_panel">
          <div className="cash_container">
            <img src={starIcon} alt="" className="star_icon" />
            <span className="cash_amount absolute_text">300</span>
            <button className="buy_cash_btn absolute_text">+</button>
          </div>
          <div className="cash_container">
            <img src={gemstoneIcon} alt="gemstone icon" className="gem_icon" />
            <span className="cash_amount absolute_text">10</span>
            <button className="buy_cash_btn absolute_text">+</button>
          </div>
        </div>
      </div>
      <div className='app_navigation'>
        <button className='app_navigation-menu_btn' onClick={() => setStore(true)}>
          <img src={store} alt='store' />
          <span>store</span>
        </button>
        <div className='app_navigation-gradient_hr'></div>
        <button className='app_navigation-menu_btn' onClick={() => setCollections(true)}>
          <img src={collections} alt='collections' />
          <span>collections</span>
        </button>
        <div className='app_navigation-gradient_hr'></div>
        <button className='app_navigation-menu_btn' onClick={() => setMode(true)}>
          <img src={mode} alt='mode' />
          <span>mode</span>
        </button>
        <div className='app_navigation-gradient_hr'></div>
        <button className='app_navigation-menu_btn' onClick={() => setTests(true)}>
          <img src={tests} alt='tests' />
          <span>tests</span>
        </button>
      </div>
      {isStore ? <StorePopup closeFunc={() => setStore(false)} /> : null}
      {isCollections ? <CollectionsPopup closeFunc={() => setCollections(false)} /> : null}
      {isMode ? <ModePopup closeFunc={() => setMode(false)} /> : null}
      {isTests ? <TestsPopup closeFunc={() => setTests(false)} /> : null}
    </>
  );
}
